.modal-backdrop {
    &.show {
        opacity: 0.7;
    }
}

.modal{
    &-dialog{
        pointer-events: unset;
        padding: 68px 68px 73px;
        background: #FFFFFF;
        border-radius: 20px;
        width: 640px;
        max-width: calc(100% - 20px);
    }
    &__close{
        position: absolute;
        top: 15px;
        right: 11px;
    }
    &__title,
    &__desc{
        text-align: center;
    }
    &__desc{
        margin-top: 30px;
        font-size: 18px;
        line-height: 25px;
        letter-spacing: -0.02em;
    }
    &__form{
        display: grid;
        grid-template-columns: 100%;
        gap: 27px 33px;
        margin-top: 50px;
        .fg{
            margin: 0;
        }
    }
    &__check{
        margin-top: 20px;
    }
    &__nav{
        margin-top: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__btn{
        padding: 0 34px;
        height: 78px;
        font-size: 18px;
    }
    &__group{
        display: grid;
        grid-template-columns: 100px calc(100% - 122px);
        gap: 22px;
        align-items: center;
        label{
            font-weight: 700;
            font-size: 20px;
        }
    }
    &__rating{
        display: flex;
        align-items: center;
    }
    &__full{
        grid-column: 1/3;
    }
}

.modal-feedback{
    .modal{
        &-dialog{
            width: 1180px;
        }
        &__form{
            grid-template-columns: 1fr 1fr;
        }
        &__nav{
            justify-content: space-between;
        }
        &__btn{
            margin: 0 0 0 20px;
        }
        &__check{
            margin: 0;
            max-width: 510px;
        }
    }
}

@media screen and (max-width: 992px){
    .modal{
        &__full{
            grid-column: 1/2;
        }
        &__group{
            order: 2;
        }
        &__full{
            order: 3;
        }
    }

    .modal-feedback{
        .modal{
            &__form{
                grid-template-columns: 100%;
            }
            &__nav{
                display: block;
            }
            &__btn{
                margin: 20px 0 0;
            }
        }
    }
}


@media screen and (max-width: 768px){
    .modal{
        &-dialog{
            padding: 30px 20px 30px;
        }
        &__form{
            gap: 10px;
            margin-top: 25px;
        }
        &__btn{
            width: 100%;
        }
    }
}
