.file-input2 {
    position: relative;
    display: flex;
    align-items: center;
    height: 53px;
    margin: 40px 0 23px;
    width: 100%;
    border-bottom: 1px solid #FFFFFF;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: -0.03em;
    color: #FFFFFF;
    svg {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }
    input {
        position: absolute;
        z-index: 2;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        cursor: pointer;
    }
    &__text {
        position: absolute;
        left: 0;
        bottom: -68px;
        font-size: 16px;
        line-height: 30px;
        letter-spacing: -0.03em;
        color: #FFFFFF;
        font-weight: 400;
    }
}

.vacancies-card {
    padding: 40px 25px;
    max-width: calc(100% - 10px);
    border: 1px solid #D9D9D9;
    border-radius: 40px;
    &__subtitle {
        font-weight: 700;
        font-size: 24px;
        line-height: 30px;
        letter-spacing: 0.01em;
        margin-bottom: 25px;
    }
    &__items{
        margin-top: 30px;
    }
    &__text {
        display: flex;
        align-items: center;
        color: $secondary;
        font-weight: 700;
        &-number {
            font-size: 18px;
            line-height: 189%;
            margin-left: 13px;
        }
        &-currency {
            font-weight: 700;
            font-size: 24px;
            line-height: 34px;
            color: #5CA649;
        }
        &-street {
            font-size: 16px;
            margin-left: 13px;
        }
        &:not(:last-child){
            margin-bottom: 4px;
        }
    }
    &__icon{
        width: 22px;
        flex-shrink: 0;
    }
    &__work {
        margin-top: 30px;
        display: flex;
        align-items: center;
        &-text {
            font-size: 16px;
            line-height: 26px;
            letter-spacing: -0.02em;
            margin-right: 10px;
        }
    }
    &__links {
        a {
            &:not(:first-child) {
                margin-left: 10px;
            }
            &:hover{
                img{
                    mix-blend-mode: normal;
                    opacity: 1;
                }
            }
        }
        img{
            mix-blend-mode: luminosity;
            opacity: 0.5;
        }
    }
    &__btn {
        margin-top: 30px;
        width: 100%;
        height: 58px;
    }
}

.vacancy {
    padding-bottom: 40px;
    &__inner {
        display: flex;
        margin-top: 50px;
    }
    &__left {
        background: #F8F8F8;
        border-radius: 40px;
        padding: 54px 38px;
        width: calc(100% - 520px);
    }
    &__right {
        background: $secondary;
        border-radius: 40px;
        padding: 60px 30px 70px;
        width: 500px;
        margin-left: 20px;
        text-align: center;
    }
    &__info {
        display: flex;
        align-items: center;
        &-text {
            display: flex;
            align-items: center;
            &:not(:first-child) {
                margin-left: 74px;
            }
        }
    }
    &__text-experience,
    &__text-street,
    &__text-number {
        font-weight: 700;
        font-size: 18px;
        line-height: 34px;
        margin-left: 13px;
    }
    &__address {
        &:hover {
            span {
                color: #5ca649;
                transition: all 0.3s;
            }
        }
    }
    &__work {
        margin-top: 45px;
    }
    &__desc {
        &:not(:first-child) {
            margin-top: 20px;
        }
    }
    &__list {
        margin-top: 20px;
        li {
            font-size: 15px;
            line-height: 17px;
            letter-spacing: -0.02em;
            padding-left: 14px;
            position: relative;
            &:not(:first-child) {
                margin-top: 11px;
            }
            &::after {
                content: '';
                position: absolute;
                top: 5px;
                left: 0;
                width: 6px;
                height: 6px;
                background: #5CA649;
                border-radius: 50%;
            }
        }
    }
    &__see {
        display: flex;
        align-items: center;
        margin-top: 58px;
        &-text {
            font-weight: 700;
            font-size: 15px;
            line-height: 24px;
            margin-right: 12px;
        }
        &-links {
            display: flex;
            align-items: center;
            a {
                &:not(:first-child) {
                    margin-left: 12px;
                }
            }
        }
    }
    &__right-title {
        font-size: 50px;
        line-height: 45px;
        letter-spacing: -0.04em;
        margin-bottom: 17px;
        color: #fff;
    }
    &__form {
        color: #fff;
        .fg__icon {
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            width: 24px;
            &~input {
                padding-left: 40px;
            }
            &--right {
                left: unset;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                width: 16px;
            }
        }
    }
    &__fg {
        margin-bottom: 17px;
        &--two {
            padding-top: 50px;
        }
        input {
            font-size: 22px !important;
        }
        &-input {
            padding-left: 0 !important;
        }
        textarea {
            color: #fff;
            border: none;
            border-bottom: 1px solid #fff;
            padding: 0;
            width: 100%;
            background-color: transparent;
            resize: none;
            font-size: 22px;
            line-height: 30px;
            &::placeholder {
                color: #FFFFFF;
            }
        }
    }
    .vacancy__check {
        margin-top: 23px;
        label {
            font-size: 15px;
            line-height: 23px;
            letter-spacing: 0.01em;
            color: #FFFFFF;
            text-align: start;
        }
    }
    &__btn {
        width: 360px;
        max-width: 100%;
        height: 78px;
        margin: 50px auto 0;
        font-size: 18px;
        svg{
            margin-right: 20px;
        }
    }
    .files{
        display: flex;
        flex-direction: column-reverse;
        align-items: unset;
    }
}

.vacancies {
    overflow: hidden;
    padding: 40px 0 140px;
    &__slider {
        margin-top: 56px;
    }
    .slider__nav{
        margin-bottom: 56px;
    }
    .swiper {
        &-container{
            overflow: hidden;
        }
        &-wrapper{
            align-items: stretch;
        }
        &-slide{
            width: calc(25% - 22.5px);
            max-width: calc(100% - 10px);
            height: auto;
        }
    }
    &__all{
        width: 164px;
        height: 58px;
        margin-top: 45px;
    }
}

@media screen and (max-width: 1350px) {
    .vacancy {
        &__info {
            display: block;
            &-text {
                &:not(:first-child) {
                    margin: 7px 0 0;
                }
            }
        }
    }

    .vacancies{
        &__slider{
            .swiper{
                &-slide{
                    width: calc(33.33% - 20px);
                }
            }
        }
    }
}

@media screen and (max-width: 1200px) {
    .vacancy {
        &__inner {
            display: block;
        }
        &__left {
            width: 100%;
        }
        &__right {
            width: 100%;
            margin: 30px 0 0;
        }
    }
}

@media screen and (max-width: 992px) {
    .file-input2 {
        margin-top: 30px;
    }
    .vacancies-card {
        &__subtitle {
            font-size: 20px;
        }
    }
    .vacancies {
        padding: 20px 0 70px;
        &__slider{
            margin-top: 30px;
            .swiper{
                &-slide{
                    width: calc(50% - 5px);
                }
            }
        }
    }

    .vacancy {
        padding-bottom: 20px;
        &__inner {
            margin-top: 20px;
        }
        &__left {
            padding: 30px;
        }
        &__right {
            padding: 30px;
            &-title {
                font-size: 30px;
                margin-bottom: 30px;
            }
        }
        &__work {
            margin-top: 30px;
        }
        &__see {
            margin-top: 30px;
        }
        &__text-experience,
        &__text-street,
        &__text-number {
            font-size: 18px;
        }
        &__subtitle {
            font-size: 20px;
        }
        &__fg {
            input,
            textarea {
                font-size: 20px !important;
            }
            &:not(:first-child) {
                margin-top: 30px;
            }
            &--two {
                padding-top: 23px;
            }
        }
        .vacancy__check {
            margin-top: 30px;
            label {
                font-size: 12px;
                padding-left: 50px;
            }
        }
        &__btn {
            margin-top: 30px;
        }
    }
}

@media screen and (max-width: 576px) {
    .vacancy {
        &__left {
            padding: 30px 15px;
        }
        &__right {
            padding: 30px 15px;
        }
    }

    .vacancies{
        &__slider{
            .swiper{
                &-container{
                    overflow: unset;
                }
                &-slide{
                    width: 90%;
                }
            }
        }
    }
}
