.licensies{
  padding-bottom: 100px;
  overflow: hidden;
  .custom-block__inner{
    padding-bottom: 0;
  }
  &__text{
    margin-top: 47px;
  }
  &__info{
    margin-top: 46px;
    background-color: $secondary;
    padding: 41px 39px 51px;
    border-radius: 40px;
  }
  &__items{
    display: flex;
    justify-content: space-between;
    max-width: 1490px;
    margin: 0 auto;
  }
  &__subtitle{
    font-weight: 700;
    font-size: 18px;
    line-height: 167%;
    letter-spacing: -0.02em;
    color: #FDBDFF;
  }
  &__desc{
    font-size: 16px;
    line-height: 150%;
    margin-top: 10px;
    color: #FFFFFF;
    &>*{
      line-height: inherit;
    }
  }
  &__slider{
    margin-top: 44px;
    position: relative;
    &--doctor{
      margin-top: 65px;
      .slider__nav{
        margin-bottom: 65px;
      }
    }
    &--absolute-btns{
      .swiper{
        &-button{
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          &-prev{
            left: -76px;
          }
          &-next{
            right: -76px;
          }
        }
      }
    }
    .swiper{
      &-container{
        overflow: hidden;
      }
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        width: calc(25% - 22.5px);
        max-width: calc(100% - 10px);
        height: auto;
      }
    }
  }
}


@media screen and (max-width: 1500px){
  .licensies{
    &__top{
      display: block;
    }
    &__tags{
      margin-top: 15px;
    }
    &__items{
      display: grid;
      grid-template-columns: repeat(4,1fr);
      gap: 30px 20px;
    }
  }
}


@media screen and (max-width: 1200px){
  .licensies{
    &__items{
      grid-template-columns: repeat(3,1fr);
    }
    &__slider{
      .swiper{
        &-slide{
          width: calc(33.33% - 20px);
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .licensies{
    padding-bottom: 50px;
    &__info{
      margin-top: 20px;
      padding: 20px;
      border-radius: 20px;
    }
    &__items{
      grid-template-columns: repeat(2,1fr);
    }
    &__slider{
      .swiper{
        &-slide{
          width: calc(50% - 5px);
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .licensies{
    &__items{
      grid-template-columns: 100%;
    }
    &__slider{
      .swiper{
        &-container{
          overflow: unset;
        }
        &-slide{
          width: 90%;
        }
      }
    }
  }
}
