.service-page{
  padding-bottom: 140px;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 22.5px));
    gap: 70px 30px;
  }
}

.comfort{
  padding: 90px 0 132px;
  background-color: $whiteBg;
  margin-bottom: 60px;
  &__title{
    text-align: center;
    max-width: 900px;
    margin: 0 auto;
  }
  &__inner{
    display: grid;
    grid-template-columns: 600px auto;
    gap: 30px 58px;
    margin-top: 65px;
  }
  &__desc{
    font-weight: 700;
    font-size: 18px;
    line-height: 133%;
    letter-spacing: -0.02em;
  }
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 35px;
    margin-top: 35px;
  }
  &__accent{
    margin-top: 35px;
    padding-top: 35px;
    border-top: 1px solid rgba(#000,.05);
    font-weight: 700;
  }
}

.comfort-card{
  background-color: #FFFFFF;
  box-shadow: 0px 14px 34px rgba(0, 0, 0, 0.01);
  border-radius: 40px;
  padding: 50px 33px;
  background-image: url(../img/brand5.svg);
  background-size: calc(100% - 100px) auto;
  background-position: center;
  background-repeat: no-repeat;
  &__top{
    display: grid;
    grid-template-columns: 80px auto;
    gap: 55px;
  }
  &__quote{
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    font-style: italic;
    font-family: $font3;
  }
  &__title{
    font-size: 24px;
    line-height: 29px;
  }
  &__item{
    margin-top: 20px;
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: 52px;
  }
  &__avatar{
    width: 130px;
    height: 130px;
    border-radius: 50%;
    padding: 5.24px;
    overflow: hidden;
    position: relative;
    border: 1px dashed $primaryHover;
    flex-shrink: 0;
    margin-right: 26px;
    img{
      border-radius: 50%;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__desc{
    margin-top: 10px;
    color: #969EAA;
    max-width: 325px;
  }
}

.service-main{
  &__inner{
    padding: 14.2% 3.94% 6.2%;
    border-radius: 40px;
    background-size: cover;
    margin-top: 47px;
    background-position: center;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    line-height: 82%;
    max-width: 750px;
    width: 100%;
    margin-right: auto;
  }
  &__subtitle{
    width: 100%;
    margin-top: 66px;
  }
  &__price{
    padding: 0 20px;
    margin: 12px auto 0 0;
    height: 56px;
    display: flex;
    align-items: center;
    background: #FFFFFF;
    border-radius: 50px;
    font-weight: 700;
    font-size: 37px;
    letter-spacing: -0.04em;
    color: #008FCD;
  }
}

.service-nav{
  display: flex;
  justify-content: space-between;
  margin-top: 43px;
  &__left{
    max-width: 802px;
  }
  &__link{
    padding: 0 40px 0 30px;
    height: 58px;
    font-size: 16px;
    svg{
      margin-right: 20px;
    }
    &:hover{
      path{
        fill:none;
      }
    }
  }
}

.service-desc{
  padding: 80px 0 40px;
  &__groups{
    display: grid;
    grid-template-columns: 100%;
    gap: 50px;
    &--gap100{
      gap: 100px;
    }
  }
}

.services2{
  padding: 40px 0;
  &__grid{
    margin-top: 52px;
    display: grid;
    grid-template-columns: repeat(3,calc(33.33% - 20px));
    gap: 30px;
  }
}

.works{
  padding: 40px 0;
  &__link{
    margin: 41px auto 0 0;
    width: 259px;
  }
}

.service-price{
  padding: 60px 0;
  &__inner{
    border-radius: 20px;
    padding: 55px;
    background-color: $whiteBg;
  }
  &__items{
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 100px;
  }
  &__item{
    padding: 20px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 18px;
    line-height: 167%;
    border-bottom: 1px solid rgba(#000, .05);
  }
  &__num{
    font-weight: 700;
    color: $primary;
    font-size: 20px;
  }
}

.advice{
  padding: 30px 0;
  &__wrap{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 50px;
    border-radius: 20px;
    background-color: $whiteBg;
    margin-top: 34px;
  }
  &__left{
    padding: 38px 0 38px 54px;
    display: grid;
    grid-template-columns: 60px auto;
    gap: 29px;
    background-image: url(../img/brand6.svg);
    background-repeat: no-repeat;
    background-position: left 33px center;
  }
  &__quote{
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    font-size: 18px;
    line-height: 139%;
    letter-spacing: -0.02em;
    font-family: $font3;
    font-style: italic;
  }
}


@media screen and (max-width: 1450px){
  .service-page{
    &__grid{
      grid-template-columns: repeat(3,calc(33.33% - 20px));
    }
  }

  .comfort{
    &__grid{
      grid-template-columns: 100%;
      gap: 15px;
    }
  }
}


@media screen and (max-width: 1300px){
  .service-price{
    &__items{
      gap: 0 50px;
    }
  }

  .services2{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 15px));
    }
  }

  .advice{
    &__wrap{
      grid-template-columns: 100%;
      gap: 20px;
    }
    &__left{
      padding: 30px 45px 0;
    }
  }
}


@media screen and (max-width: 1200px){
  .service-page{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 15px));
    }
  }

  .comfort{
    &__inner{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .service-page{
    padding-bottom: 70px;
  }

  .comfort{
    padding: 45px 0 65px;
    &__inner{
      margin-top: 32px;
    }
    &__grid{
      margin-top: 20px;
    }
    &__accent{
      margin-top: 15px;
      padding-top: 15px;
    }
  }

  .service-main{
    &__inner{
      margin-top: 0;
      border-radius: 20px;
    }
    &__subtitle{
      margin-top: 35px;
    }
    &__price{
      font-size: 25px;
      height: 40px;
      padding: 0 15px;
    }
  }

  .service-nav{
    display: block;
    &__right{
      margin-top: 40px;
      display: flex;
    }
    &__link{
      width: 100%;
    }
  }

  .service-desc{
    padding: 30px 0;
    &__groups{
      gap: 25px;
    }
  }

  .works{
    &__link{
      width: 100%;
    }
  }

  .service-price{
    padding: 30px 0;
    &__inner{
      padding: 20px;
      border-radius: 10px;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }

  .advice{
    &__left{
      padding: 20px 15px 0;
    }
  }
}


@media screen and (max-width: 768px){
  .services2{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .service-page{
    &__grid{
      grid-template-columns: 100%;
      gap: 40px;
    }
  }

  .comfort-card{
    padding: 25px 16px;
    border-radius: 20px;
    &__top{
      grid-template-columns: 25px auto;
      gap: 15px;
    }
    &__bottom{
      display: block;
      margin-top: 30px;
    }
    &__avatar{
      margin: 0 auto 15px;
    }
    &__title{
      font-size: 18px;
    }
    &__item{
      margin-top: 15px;
    }
  }

  .service-price{
    &__item{
      padding: 10px 0;
    }
    &__num{
      display: block;
      margin-top: 10px;
    }
  }
}
