.doctor-main{
  padding-bottom: 60px;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 25px));
    gap: 50px;
  }
}

.container{
  max-width: 1670px;
}

.doctor-info{
  .display2{
    letter-spacing: -.04em;
  }
  &__grid{
    margin-top: 30px;
    display: grid;
    grid-template-columns: 41% calc(59% - 30px);
    gap: 30px;
  }
  &__col2{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 20px;
    }
  }
  &__prof{
    font-size: 18px;
    line-height: 144%;
    text-transform: uppercase;
    color: $primaryHover;
    max-width: 350px;
  }
  &__experience{
    padding: 0 20px;
    height: 42px;
    background: rgba($primaryHover, 0.1);
    display: flex;
    align-items: center;
    margin-top: 24px;
    border-radius: 100px;
    span{
      color: $primaryHover;
    }
  }
  .info-group{
    margin-top: 24px;
  }
}

.info-group{
  &__top{
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;
    img{
      width: 36px;
      flex-shrink: 0;
      margin-right: 10px;
    }
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: 5px;
  }
  &__item{
    font-size: 16px;
    line-height: 147%;
    margin: 5px 30px 0 0;
  }
}

.doctor-form{
  padding: 20px 40px;
  border: 1px solid #5db02a;
  border-radius: 15px;
  background: #eff8ea;
  &__fields{
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 24px;
  }
  .fg{
    margin: 0;
    textarea,
    input{
      background: #fff;
      border: 1px solid #fff;
      height: 59px;
      border-radius: 15px;
      padding: 0 25px;
      font-size: 16px;
      &:focus{
        border-color: $primaryHover;
      }
      &::placeholder{
        color: #2d343e;
      }
    }
    textarea{
      padding-top: 18px;
      padding-bottom: 18px;
      height: 125px;
    }
  }
  &__nav{
    display: flex;
    margin-top: 24px;
  }
  &__btn{
    padding: 0 25px;
    height: 59px;
    border-radius: 15px;
    font-size: 16px;
    letter-spacing: unset;
  }
}




@media screen and (max-width: 1200px) {
  .doctor-main{
    &__inner{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 768px) {
  .doctor-info{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      max-width: 250px;
    }
  }

  .doctor-form{
    padding: 15px;
    &__btn{
      width: 100%;
    }
  }
}
