.doctors{
  &__links{
    margin: 16px 0 42px;
  }
  &__tags{
    margin-top: 24px;
  }
  &__tags2{
    margin: 1px 0 56px;
  }
}

.doctors-page{
  padding-bottom: 80px;
}

.doctor{
  padding-bottom: 75px;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 530px) 500px;
    gap: 30px;
    position: relative;
  }
  &__content{
    max-width: 951px;
  }
  &__img{
    max-width: 500px;
    position: absolute;
    width: 500px;
    right: 0;
    top: 0;
    img{
      width: 100%;
      display: block;
      border-radius: 310px;
    }
  }
  &__prof,
  &__experience{
    font-weight: 700;
    font-size: 18px;
    line-height: 144%;
    color: $primary;
  }
  &__prof{
    margin-top: 40px;
  }
  &__experience{
    span{
      font-weight: 700;
    }
  }
  &__desc{
    margin-top: 40px;
    display: flex;
    &:before{
      content: '';
      width: 22px;
      height: 23px;
      left: 0;
      top: 0;
      display: block;
      margin-right: 15px;
      flex-shrink: 0;
      background-image: url(../img/quote.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% auto;
    }
  }
  &__info{
    margin-top: 40px;
    &-title{
      display: flex;
      align-items: center;
      line-height: 192%;
      font-weight: 700;
      font-size: 24px;
      letter-spacing: -0.02em;
      svg{
        margin-right: 10px;
      }
    }
    .link{
      margin: 13px 30px 0 0;
      line-height: 189%;
    }
  }
  &__btn{
    margin-top: 40px;
    width: 321px;
    height: 72px;
    font-size: 18px;
    svg{
      margin-right: 20px;
    }
    &:hover{
      path{
        fill: none;
      }
    }
  }
}

.page-nav{
  &__inner{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  .link{
    font-size: 24px;
    line-height: 192%;
    letter-spacing: -0.02em;
    @media screen and (min-width: 992px){
      margin-right: 50px;
    }
    &--active{
      color: $secondary;
    }
  }
}

.edu{
  margin: 85px 0 105px;
  padding: 70px 0 100px;
  background-color: $whiteBg;
  overflow: hidden;
  &__title{
    margin-bottom: 88px;
  }
  .mySwiper{
    overflow: unset;
    transform: translateX(-19px);
    .swiper{
      &-slide{
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
        letter-spacing: 0.177778px;
        width: 80px;
        height: 42px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 30px;
        &-thumb-active{
          background-color: $primaryHover;
          color: #fff;
          font-weight: 700;
        }
      }
    }
  }
  .mySwiper2{
    margin-top: 65px;
  }
  .swiper-scrollbar{
    width: 100%;
    height: 1px;
    background: #EEEEEE;
    margin-top: 25px;
    position: relative;
    bottom: unset;
    &-drag{
      background-color: $black;
    }
  }
}

.edu-card2{
  display: flex;
  align-items: center;
  &__year{
    width: 255px;
    height: 255px;
    background: #FFFFFF;
    box-shadow: 0px 44px 44px rgba(76, 156, 26, 0.04);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 78px;
    letter-spacing: -0.04em;
    color: $primaryHover;
    margin-right: 60px;
    flex-shrink: 0;
  }
  &__content{
    max-width: 950px;
  }
  &__title{
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: -0.02em;
  }
  &__desc{
    font-size: 18px;
    line-height: 26px;
    letter-spacing: -0.02em;
    margin-top: 15px;
  }
}

.licensy-block{
  overflow: hidden;
  .custom-block__inner{
    padding-bottom: 0;
  }
  .custom-block__top{
    @media screen and (min-width: 992px){
      margin-bottom: 70px;
    }
  }
}

.consult-doctor{
  @media screen and (min-width: 992px){
    .consult{
      &__desc{
        margin-top: 20px;
      }
    }
  }
}

.works{
  &__tags{
    margin: 24px 0 51px;
  }
}


@media screen and (max-width: 1300px) {
  .doctor {
    &__inner {
      grid-template-columns: calc(100% - 450px) 420px;
    }
    &__img{
      width: 420px;
    }
  }
}


@media screen and (max-width: 1200px) {
  .doctor {
    &__inner {
      grid-template-columns: 100%;
    }
    &__img{
      position: static;
      margin-top: 15px;
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .doctor{
    padding-bottom: 35px;
    &__prof,
    &__experience{
      font-size: 16px;
    }
    &__info,
    &__btn{
      margin-top: 30px;
    }
    &__img{
      img{
        border-radius: 10px;
      }
    }
  }

  .edu{
    padding: 35px 0 50px;
    margin: 42px 0 52px;
    &__title{
      margin-bottom: 44px;
    }
  }

  .edu-card2{
    flex-direction: column;
    text-align: center;
    &__year{
      margin: 0 0 30px;
    }
  }

  .works{
    .slider{
      &__nav{
        display: block;
        position: static;
        .swiper-button{
          background-color: #fff;
          position: absolute !important;
          &-prev{
            left: -15px;
          }
          &-next{
            right: -15px;
            margin-left: 0 !important;
          }
        }
      }
    }
  }
}
