.price{
  padding-bottom: 130px;
  .note__inner{
    margin-top: 54px;
  }
  &__tags{
    margin-top: 24px;
  }
  &__groups{
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
  }
  &__hint{
    font-size: 14px;
    line-height: 171%;
    margin-top: 50px;
    &>*{
      line-height: inherit;
    }
  }
  @media screen and (min-width: 992px){
    .service-price__inner{
      padding-top: 45px;
      padding-bottom: 45px;
    }
    .service-price__items{
      grid-template-columns: 100%;
    }
    .service-price__title{
      font-size: 35px;
    }
  }
  .consult-card{
    position: sticky;
    top: 20px;
  }
}


@media screen and (max-width: 992px){
  .price{
    &__tags{
      flex-wrap: wrap;
    }
  }
}
