.contacts{
  padding-bottom: 85px;
  &__info{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 40px;
  }
  &__socials{
    &-title{
      margin-right: 13px;
    }
    .social__items{
      margin: 0;
    }
    .social__item{
      width: 25px;
      height: 25px;
    }
  }
  &__tags{
    margin-top: 30px;
  }
  &__phone{
    font-weight: 700;
    font-size: 20px;
  }
}

.contacts-desc{
  margin-top: 50px;
  &__images{
    margin-top: 50px;
    display: grid;
    grid-template-columns: repeat(3,calc(33.33% - 20px));
    gap: 30px;
  }
  &__img{
    padding-bottom: 66%;
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    img{
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.conf{
  padding-bottom: 82px;
  &__content{
    margin-top: 50px;
    max-width: 1152px;
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 30px;
    }
    p{
      &:not(:last-child){
        margin-bottom: 0;
      }
    }
    strong{
      font-weight: 700;
    }
  }
}


@media screen and (max-width: 1300px){
  .contacts{
    &__info{
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
      margin: 30px 0;
    }
  }

  .contacts-desc{
    &__images{
      grid-template-columns: repeat(2,calc(50% - 15px));
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding-bottom: 45px;
  }

  .contacts-desc{
    margin-top: 30px;
    &__images{
      margin-top: 30px;
      grid-template-columns: repeat(2,calc(50% - 7.5px));
      gap: 15px;
    }
    &__img{
      border-radius: 10px;
    }
  }

  .conf{
    padding-bottom: 40px;
    &__content{
      margin-top: 25px;
    }
    &__group {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__socials{
      &-title{
        display: none;
      }
    }
  }

  .contacts-desc{
    &__images{
      grid-template-columns: 100%;
    }
  }
}
