.about{
  padding-bottom: 70px;
  &__title{
    span{
      color: $primaryHover;
    }
  }
  &__grid{
    margin-top: 50px;
    display: grid;
    grid-template-columns: 49% 47.1%;
    justify-content: space-between;
    gap: 2%;
  }
  &__text{
    max-width: 668px;
  }
  &__link{
    font-size: 15px;
    line-height: 167%;
    font-weight: 700;
    letter-spacing: -0.02em;
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
  &__img{
    border-radius: 20px;
    overflow: hidden;
    img{
      border-radius: 50px;
      width: 100%;
      display: block;
    }
  }
  &__map-link{
    width: 220px;
    height: 58px;
    margin-top: 36px;
    svg{
      margin-right: 12px;
    }
  }
}

.youtube-video{
  position: relative;
  padding-bottom: 61.3%;
  border-radius: 50px;
  overflow: hidden;
  iframe{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.doctors--gray,
.doctors--about{
  background: #F8F8F8;
  .doctor-card{
    background-color: #fff;
  }
}

.doctors--about{
  padding: 70px 0 112px;
}

.doctors--gray{
  padding: 90px 0;
}

.about2{
  padding: 96px 0 108px;
  @media screen and (min-width: 992px){
    .about__grid{
      margin-top: 0;
      grid-template-columns: 47.1% 49%;
    }
  }
  .about__text{
    margin-top: 36px;
  }
}

.clinics{
  &__desc{
    font-size: 24px;
    line-height: 192%;
    letter-spacing: 0.01em;
    margin-bottom: 24px;
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 49px;
    }
  }
}

.about--reverse{
  @media screen and (min-width: 992px){
    .about__grid{
      grid-template-columns: 47.1% 49%;
    }
    .about__video{
      order: 2;
    }
  }
}


@media screen and (max-width: 992px){
  .about{
    &__grid{
      grid-template-columns: 100%;
      gap: 20px;
    }
    &__text{
      max-width: 100%;
    }
  }

  .youtube-video{
    padding-bottom: 55%;
    border-radius: 10px;
  }

  .doctors--about{
    padding: 35px 0 56px;
  }

  .doctors--gray{
    padding: 45px 0;
  }

  .about2{
    padding: 48px 0 54px;
    .about__grid{
      margin: 0;
    }
  }
}
