@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-primary,
.mbtn-black-outline,
.mbtn-white,
.mbtn-secondary-outline,
.mbtn-secondary{
    border-radius: 50px;
    font-weight: 700;
    font-size: 15px;
    letter-spacing: -0.02em;
    &:hover,
    &:active{
        color: #fff;
        path{
            fill:#fff;
        }
    }

}

.mbtn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        border-color: #49A611;
        background-color: #49A611;
    }
    &:active{
        border-color: #68C52F;
        background-color: #68C52F;
    }
}

.mbtn-black-outline{
    border: 1px solid #D9D9D9;
    color: #2D343E;
    background: #FCFCFC;
    &:hover{
        background-color: $black;
    }
}

.mbtn-white{
    background: #FFFFFF;
    color: $primaryHover;
    &:hover{
        border-color: $primaryHover;
        background-color: $primaryHover;
        color: #fff;
    }
}

.mbtn-secondary-outline{
    color: $secondary;
    border: 1px solid $secondary;
    &:hover{
        color: #fff;
        border-color: #99269D;
        background-color: #99269D;
    }
    &:active{
        color: #fff;
        border-color: #CA40CF;
        background-color: #CA40CF;
    }
}

.mbtn-secondary{
    color: #fff;
    background-color: $secondary;
    &:hover{
        color: #fff;
        background-color: #99269D;
    }
    &:active{
        color: #fff;
        background-color: #CA40CF;
    }
}

.links-wrap{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 16px;
    .mlink{
        margin: 16px 60px 0 0;
    }
}

.mlink{
    font-size: 18px;
    line-height: 133%;
    text-decoration-line: underline;
    &.mlink--active{
        font-weight: 800;
    }
}


@media screen and (max-width: 992px){
    .links-wrap{
        .mlink{
            margin-right: 30px;
        }
    }
}
