.services-items{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 15px));
  gap: 30px;
  margin-top: 60px;
}

.service-card4{
  min-height: 194px;
  overflow: hidden;
  padding: 32px 320px 32px 32px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 40px;
  background: #f8f8f8;
  position: relative;
  &__title{
    font-weight: 700;
    font-size: 28px;
    line-height: 100%;
    letter-spacing: -0.02em;
    color: $secondary;
  }
  &__desc{
    font-size: 18px;
    line-height: 128%;
    margin-top: 20px;
  }
  &__img{
    position: absolute;
    left: 100%;
    margin-left: -293px;
    top: 0;
    height: 100%;
    object-fit: cover;
  }
}


@media screen and (max-width: 1200px){
  .services-items{
    margin-top: 30px;
    grid-template-columns: 100%;
    gap: 20px;
  }
}


@media screen and (max-width: 576px){
  .service-card4{
    padding: 20px;
    &__img{
      display: none;
    }
  }
}
