.note{
  padding-bottom: 40px;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 530px) 500px;
    gap: 30px;
  }
  &__plan{
    margin: 40px 0 60px;
    &-title{
      font-weight: 700;
      font-size: 20px;
      line-height: 120%;
    }
    &-links{
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 15px;
    }
    &-link{
      margin: 0 auto 8px 0;
      font-weight: 500;
      font-size: 16px;
      line-height: 188%;
      color: $primary;
    }
  }
}

.notes--note-page{
  padding-bottom: 130px;
}

.note-info{
  display: flex;
  align-items: center;
  margin-top: 40px;
  &:not(:last-child){
    margin-bottom: 40px;
  }
  &__item{
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 15px;
    line-height: 16px;
    letter-spacing: 0.01em;
    &:not(:last-child){
      margin-right: 34px;
    }
  }
  &__icon{
    margin-right: 10px;
    flex-shrink: 0;
    width: 18px;
    img{
      width: 100%;
      display: block;
    }
  }
}

._add-like{
  cursor: pointer;
}

.content-note{
  h3{
    font-weight: 700;
    font-size: 35px;
    line-height: 110%;
    margin: 50px 0 25px;
    letter-spacing: -0.04em;
  }
  &>img{
    display: block;
    width: 100%;
    margin: 40px 0;
    border-radius: 40px;
  }
}

.author{
  border-radius: 40px;
  background-color: $whiteBg;
  overflow: hidden;
  padding: 60px 32px;
  margin-bottom: 35px;
  &__img{
    width: 248px;
    height: 248px;
    border-radius: 50%;
    margin: 0 auto;
    padding: 10px;
    overflow: hidden;
    position: relative;
    border: 1px dashed $primaryHover;
    img{
      border-radius: 50%;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    text-align: center;
    margin-top: 41px;
  }
  &__subtitle,
  &__desc{
    font-size: 16px;
    line-height: 162%;
    letter-spacing: -0.02em;
  }
  &__desc{
    color: #969EAA;
  }
  &__title{
    margin: 10px 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 125%;
    letter-spacing: -0.04em;
    color: $primaryHover;
  }
}

.consult-card{
  padding: 61px 32px 74px;
  border-radius: 20px;
  background-color: $secondary;
  color: #fff;
  text-align: center;
  position: sticky;
  top: 20px;
  @media screen and (min-width: 992px){
    .consult__title{
      font-size: 50px;
      line-height: 93%;
    }
    .consult__desc{
      font-size: 18px;
      margin-top: 30px;
    }
    .consult__form{
      margin-top: 40px;
    }
    .fg{
      margin-bottom: 24px;
    }
    .consult__check{
      margin-top: 45px;
    }
    .consult__btn{
      margin: 45px auto 0;
    }
  }
  .consult__check{
    text-align: left;
  }
}

.note-bottom{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 40px;
  .note-info{
    margin: 0 !important;
  }

}

.note-social{
  display: flex;
  align-items: center;
  &__title{
    font-size: 15px;
    line-height: 16px;
    letter-spacing: 0.01em;
    margin-right: 20px;
  }
  .social__items{
    margin: 0;
  }
  .social__item{
    width: 25px;
    height: 25px;
    margin: 0 5.5px;
  }
}

.children{
  padding: 100px 0 60px;
  &__inner{
    padding: 77px 6.8% 91px 7.5%;
    background-image: url(../img/consult-bg.webp);
    background-size: cover;
    background-position: center;
    background-color: $primaryHover;
    position: relative;
    border-radius: 260px;
  }
  &__title{
    font-size: 85px;
    line-height: 116%;
    width: 68%;
    color: #fff;
    span{
      font-weight: 700;
    }
  }
  &__img{
    position: absolute;
    right: 6.8%;
    bottom: 0;
    width: 31.94%;
    display: block;
  }
}


@media screen and (max-width: 1500px){
  .children{
    &__inner{
      border-radius: 80px;
    }
    &__title{
      font-size: 70px;
    }
  }
}


@media screen and (max-width: 1300px){
  .note{
    &__inner{
      grid-template-columns: calc(100% - 450px) 420px;
    }
  }

  .children{
    &__inner{
      padding: 30px;
    }
  }
}


@media screen and (max-width: 1200px){
  .note{
    &__inner{
      grid-template-columns: 100%;
    }
    &__sidebar{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 30px;
    }
  }

  .author{
    margin: 0;
  }
}


@media screen and (max-width: 992px){
  .note{
    &__sidebar{
      grid-template-columns: 100%;
    }
    &__plan{
      margin: 20px 0 30px;
    }
  }

  .note-info{
    margin: 20px 0;
  }

  .content-note{
    &>img{
      margin: 20px 0;
      border-radius: 10px;
    }
    h3{
      margin: 25px 0 12px;
    }
  }

  .author{
    border-radius: 10px;
    padding: 30px 16px;
    &__img{
      width: 180px;
      height: 180px;
    }
  }

  .consult-card{
    border-radius: 10px;
    padding: 20px;
  }

  .note-bottom{
    display: block;
  }

  .note-social{
    margin-top: 25px;
    &__title{
      margin-right: 15px;
    }
  }

  .children{
    padding: 30px 0;
    &__inner{
      border-radius: 10px;
      padding-bottom: 0;
    }
    &__title{
      width: 100%;
      font-size: 40px;
    }
    &__img{
      width: 400px;
      max-width: 100%;
      margin: 20px auto 0;
      position: static;
    }
  }
}
