.connection {
  color: #fff;
  padding: 145px 0;
  background-image: url(../img/consult-bg.webp);
  background-color: #61A935;
  &__title {
    text-align: center;
  }
  &__full{
    @media screen and (min-width: 992px){
      grid-column: 2/3;
      grid-row: 1/4;
      textarea{
        height: 100%;
      }
    }
  }
  &__subtitle {
    font-size: 20px;
    line-height: 28px;
    letter-spacing: 0.01em;
    color: #9CDA8C;
    margin-top: 20px;
  }
  &__text {
    font-size: 20px;
    line-height: 28px;
    letter-spacing: -0.02em;
    margin: 20px auto 0;
    text-align: center;
    max-width: 720px;
  }
  &__form {
    margin-top: 55px;
    &-top {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 0px 70px;
    }
    &-bottom {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 35px;
    }
  }
  &__btn {
    width: 217px;
    height: 78px;
    font-size: 18px;
    color: $secondary;
  }
  &__check {
    max-width: 480px;
    label {
      letter-spacing: -0.02em;
      color: #FFFFFF;
    }
  }
}


@media screen and (max-width: 1350px) {
  .connection {
    &__full{
      grid-column: 1/2;
    }
  }
}


@media screen and (max-width: 992px) {
  .connection {
    padding: 30px 0;
    &__subtitle {
      font-size: 16px;
      margin-top: 15px;
    }
    &__text {
      max-width: 100%;
      margin-top: 15px;
      font-size: 16px;
    }
    &__form {
      margin-top: 30px;
      &-top{
        grid-template-columns: 100%;
      }
      &-bottom {
        flex-flow: column wrap;
        align-items: unset;
        margin-top: 30px;
      }
    }
    &__btn {
      margin-right: 0;
      order: 1;
      margin-top: 30px;
    }
  }
}
