._contrast{
  .header-top{
    background: #000;
  }

  .header{
    &__project,
    &__title{
      font-size: 20px;
      color: #fff;
    }
    &__phone,
    &__mail{
      font-size: 20px;
    }
    &__info{
      &-item{
        font-size: 20px;
        svg{
          display: none;
        }
      }
    }
    &__socials{
      display: none;
    }
    &__service{
      &-btn{
        svg{
          display: none;
        }
      }
    }
  }

  .mbtn-black-outline,
  .mbtn-white,
  .mbtn-primary{
    background: #000;
    color: #fff;
    font-size: 20px;
    svg{
      display: none;
    }
  }

  .nav{
    &__link{
      font-size: 22px;
      &-badge{
        background: #000;
      }
    }
  }

  .menu{
    background: #000;
    color: #fff;
    &__tab{
      font-size: 25px;
      svg{
        display: none;
      }
      &--active{
        color: #000;
        &:before{
          border-radius: 0;
          background-color: #fff;
        }
      }
    }
    &__link{
      font-size: 20px;

    }
  }



  .main{
    .swiper-container{
      background: #000;
    }
  }

  .advantage-card{
    &__title{
      font-size: 27px;
    }
    &__desc{
      font-size: 20px;
    }
  }

  .slider{
    .swiper-scrollbar{
      height: 20px;
      &-drag{
        background: #000;
      }
    }
  }

  .service-card{
    &__title{
      font-size: 32px;
    }
  }

  .doctor-card{
    &__name{
      font-size: 32px;
    }
    &__prof,
    &__experience{
      font-size: 21px;
      line-height: 110%;
      margin-top: 10px;
      color: #000;
    }
    &__desc{
      font-size: 19px;
    }
  }

  .primary-text{
    color: #000;
  }

  body{
    color: #000;
  }

  .feedback-card{
    &__by{
      font-size: 28px;
    }
    &__for{
      font-size: 24px;
    }
    &__site,
    &__date{
      font-size: 21px;
    }
    &__text{
      font-size: 20px;
    }
    &__answer{
      &-icon{
        display: none;
      }
      .gray-text{
        color: #000;
      }
    }
    &__course{
      font-size: 23px;
      color: #000;
    }
  }

  .body-text{
    font-size: 20px;
  }

  .consult{
    background: #000;
    &__btn{
      background-color: #fff;
      color: #000;
    }
  }

  .tag{
    height: 45px;
    font-size: 25px;
    border-color: #000;
    margin: 20px 20px 0 0;
  }

  .note-card{
    &__title{
      font-size: 28px;
    }
    &__name{
      font-size: 24px;
    }
    &__info{
      &-item{
        font-size: 20px;
      }
      &-icon{
        width: 25px;
      }
    }
    &__badge{
      background: #000 !important;
      height: 45px;
      font-size: 20px;
    }
  }

  .note-info{
    &__item{
      font-size: 20px;
    }
    &__icon{
      width: 25px;
    }
  }

  .social{
    &__desc{
      font-size: 22px;
    }
  }

  .social-card{
    &__likes{
      font-size: 20px;
      height: 42px;
    }
    &__like{
      width: 30px;
      height: 30px;
    }
  }

  .footer{
    &__title{
      color: #000;
    }
    &__title,
    &__link,
    &__clinic,
    &__mail,
    &__time,
    &__protect,
    &__privacy{
      font-size: 22px;
      svg{
        display: none;
      }
    }
    &__phone{
      font-size: 25px;
    }
    &__logo2{
      width: 200px;
    }
    .social__item{
      width: 35px;
      img{
        width: 100%;
      }
    }
  }

  .address{
    &__item{
      font-size: 22px;
    }
  }

  .about{
    &__content{
      background: #000;
      color: #fff;
    }
    &__link{
      color: #fff;
      font-size: 22px;
    }
  }

  .licensies{
    &__info{
      background: #000;
    }
    &__text{
      color: #fff;
    }
    &__subtitle{
      font-size: 22px;
      color: #fff;
    }
    &__desc{
      font-size: 20px;
      color: #fff;
    }
  }

  .docs{
    &__content{
      background: #000;
      color: #fff;
    }
    &__subtitle{
      font-size: 25px;
    }
  }

  .breadcrumbs{
    &__item{
      font-size: 25px;
    }
  }

  .pagination{
    &__item{
      min-width: 55px;
    }
    &__item,
    &__next{
      border-color: #000;
      height: 55px;
      font-size: 20px;
    }
    &__next{
      svg{
        display: none;
      }
    }
  }

  .note{
    &__plan{
      &-title{
        font-size: 25px;
      }
      &-link{
        font-size: 22px;
        color: #000;
      }
    }
  }

  .author{
    &__subtitle{
      font-size: 22px;
    }
    &__title{
      font-size: 28px;
    }
    &__desc{
      font-size: 20px;
    }
  }

  .consult-card{
    background: #000;
    &__desc{
      font-size: 22px;
    }
  }

  .fg{
    input{
      font-size: 22px;
    }
  }

  .tax{
    &__title{
      font-size: 28px;
    }
    &__subtitle{
      font-size: 22px;
    }
    &__list{
      li{
        font-size: 18px;
      }
    }
  }

  .tax-form{
    background: #000;
    &__btn{
      background-color: #fff;
      color: #000;
    }
  }

  .mcheck{
    label{
      font-size: 20px;
    }
  }

  .display5{
    font-size: 28px;
  }

  .display6,
  .display3{
    font-size: 32px;
  }

  .service-main{
    &__inner{
      background: #000;
    }
    &__subtitle{
      font-size: 22px;
    }
    &__btn{
      background-color: #fff;
      color: #000;
    }
  }

  .service-nav,
  .service-price{
    &__item{
      font-size: 22px;
    }
  }

  .work-card{
    &__item,
    &__time{
      font-size: 20px;
      &-icon{
        display: none;
      }
    }
    &__desc{
      font-size: 20px;
    }
  }

  .all-services{
    &__link{
      font-size: 20px;
    }
  }

  .doctor{
    &__prof,
    &__experience{
      font-size: 23px;
      margin-top: 10px;
    }
  }

  .link{
    font-size: 22px;
  }

  .connection{
    background: #000;
  }

  .job-openings{
    &__desc{
      p{
        font-size: 20px;
        line-height: 150%;
      }
    }
  }

  .schedule{
    &__item{
      &-text{
        color: #000;
        font-size: 20px;
        line-height: 150%;
      }
    }
    &__teacher{
      &-title{
        color: #000;
        font-size: 20px;
        line-height: 150%;
      }
      &-subtitle{
        font-size: 23px;
      }
    }
  }

  .our-clinics{
    &__name{
      font-size: 27px;
    }
  }

  .vacancies-card{
    &__subtitle{
      font-size: 28px;
    }
    &__text{
      &-street{
        font-size: 22px;
      }
    }
    &__work{
      &-text{
        font-size: 20px;
      }
    }
  }

  .vacancy{
    &__right{
      background: #000;
    }
    &__list{
      li{
        font-size: 20px;
        line-height: 120%;
        &:after{
          top: 8px;
          background: #000;
        }
      }
    }
    &__info{
      &-text{
        font-size: 22px;
      }
    }
    &__see{
      &-text{
        font-size: 20px;
      }
    }
    &__btn{
      background-color: #fff;
      color: #000;
    }
  }

  .franchise{
    &__inner{
      background: #000;
      &:after{
        display: none;
      }
    }
    &__btn{
      background-color: #fff;
      color: #000;
    }
  }

  .principle{
    &__text{
      font-size: 27px;
    }
    &__item{
      &-title{
        font-size: 27px;
      }
      &-text{
        font-size: 22px;
      }
    }
  }

  .proposal{
    &__bg{
      background: #000;
    }
    &__info{
      &-title{
        color: #000;
      }
    }
    &__btn{
      background-color: #fff;
      color: #000;
    }
  }

  .investment{
    &__bg{
      background: #000;
    }
    &__item{
      &-title{
        color: #000;
      }
    }
  }

  .mistake{
    &__subtitle{
      color: #000;
    }
    &__text{
      font-size: 24px;
    }
    &__link{
      font-size: 22px;
    }
  }

  .modal{
    &__desc{
      font-size: 24px;
    }
  }
}
