.tax{
  padding-bottom: 60px;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 15px));
    gap: 30px;
    margin-top: 59px;
  }
  &__col{

  }
  &__title{
    font-weight: 700;
    font-size: 24px;
    line-height: 125%;
    letter-spacing: -0.02em;
  }
  &__group{
    margin-top: 16px;
  }
  &__subtitle{
    font-weight: 700;
    font-size: 15px;
    line-height: 160%;
  }
  &__list{
    li{
      margin-top: 9px;
      font-size: 15px;
      line-height: 156%;
      display: flex;
      align-items: center;
      &:before{
        content: '';
        width: 6px;
        height: 6px;
        display: block;
        border-radius: 50%;
        background-color: $primary;
        flex-shrink: 0;
        margin-right: 8px;
      }
    }
  }
}

.tax-form{
  background-color: $primary;
  color: #fff;
  padding: 120px 0 108px;
  &__title{
    max-width: 1250px;
    letter-spacing: -0.04em;
  }
  &__grid{
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - (75px / 4)));
    gap: 25px;
  }
  @media screen and (min-width: 992px){
    &__13{
      grid-column: 1/3;
    }
    &__35{
      grid-column: 3/5;
    }
    &__15{
      grid-column: 1/5;
    }
  }

  .fg{
    margin: 0;
  }
  &__subtitle{
    margin: 45px 0 10px;
  }
  &__checks{
    display: flex;
    flex-wrap: wrap;
    margin-top: -10px;
  }
  &__check{
    margin: 10px 32px 0 0;
  }
  &__check2{
    max-width: 1182px;
  }
  &__btn{
    margin-top: 30px;
    width: 247px;
    height: 78px;
    font-size: 18px;
    color: $black;
  }
}


@media screen and (max-width: 1300px){
  .tax{
    &__col{
      padding: 30px 20px;
      border-radius: 10px;
    }
  }
}


@media screen and (max-width: 992px){
  .tax{
    padding-bottom: 30px;
    &__grid{
      margin-top: 22px;
      grid-template-columns: 100%;
      gap: 15px;
    }
    &__col{
      padding: 20px;
    }
  }

  .tax-form{
    padding: 40px 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__subtitle{
      margin: 15px 0 5px;
    }
    &__checks{
      display: block;
    }
    &__btn{
      margin-top: 15px;
      width: 100%;
    }
  }
}
