.rating {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    &--read-only {
        pointer-events: none;
    }
    &__item {
        transition: all .3s;
        font-size: 25px;
        color: #CFCFCF;
        &:not(:first-child) {
            margin-right: 6px;
        }
        &:hover {
            cursor: pointer;
            color: #F0BE42;
            &~.rating__item {
                color: #F0BE42;
            }
        }
    }
}

.rating[data-total-rating="1"] .rating__item:nth-child(n+5),
.rating[data-total-rating="2"] .rating__item:nth-child(n+4),
.rating[data-total-rating="3"] .rating__item:nth-child(n+3),
.rating[data-total-rating="4"] .rating__item:nth-child(n+2),
.rating[data-total-rating="5"] .rating__item:nth-child(n+1) {
    color: #F0BE42;
}

.fg {
    position: relative;
    margin: 0 0 45px;
    input,
    select,
    textarea {
        border: none;
        border-bottom: 1px solid $black;
        padding: 0;
        height: 76px;
        width: 100%;
        font-size: 20px;
        background-color: transparent;
        color: $black;
        &::placeholder {
            color: $black;
        }
    }
    textarea {
        padding: 23px 0;
        resize: none;
        height: 100px;
    }
    &__icon {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 24px;
        &~input {
            padding-left: 40px;
        }
    }
    &--white {
        select,
        input,
        textarea {
            border-color: #fff;
            color: #fff;
            &::placeholder {
                color: #fff;
            }
        }
        select {
            option {
                padding: 0 10px;
                background: $black;
            }
        }
    }
}

.mcheck {
    position: relative;
    input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        cursor: pointer;
        &:checked+label {
            &::before {
                background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 11L11 17L21 8' stroke='%235CA649' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
                background-repeat: no-repeat;
                background-position: center;
            }
        }
    }
    label {
        padding-left: 63px;
        position: relative;
        font-size: 16px;
        min-height: 30px;
        line-height: 150%;
        cursor: pointer;
        display: flex;
        align-items: center;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            border-radius: 4px;
            border: 1px solid #D9D9D9;
            width: 30px;
            height: 30px;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    &--white {
        label:before {
            background-color: #fff;
            border-color: #fff;
        }
        input:checked+label::before {
            background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 11L11 17L21 8' stroke='%231F2123' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
        }
    }
}

input[type="date"]::-webkit-calendar-picker-indicator {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: auto;
    height: auto;
    color: transparent;
    background: transparent;
    opacity: 0;
}

input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-clear-button {
    z-index: 1;
}

input[type="date"]::before {
    content: attr(data-placeholder);
    width: 100%;
    //color: #5e6162;
    color: inherit;
}

input[type="date"]:focus::before,
input[type="date"]:valid::before {
    display: none
}

.files {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: -15px;
}

.file-input {
    padding: 0 32px;
    height: 58px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 16px;
    position: relative;
    border: 1px solid #fff;
    margin-top: 15px;
    input {
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        cursor: pointer;
    }
    svg {
        margin-right: 10px;
    }
    span {
        white-space: nowrap;
    }
}

.file {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 148%;
    letter-spacing: 0.01em;
    margin-right: 32px;
    margin-top: 15px;
    svg {
        margin-left: 10px;
    }
}