.lab{
  &__desc{
    margin-top: 30px;
  }
  &__tags{
    margin-top: 55px;
  }
  &__content{
    padding: 78px 0 68px;
    background-color: $whiteBg;
  }
  .tag{
    border-radius: 25px 25px 0 0;
    height: 64px;
    padding: 0 25px;
    border-bottom: none;
    font-size: 18px;
    letter-spacing: -0.04em;
    margin-right: 15px;
    span{
      text-decoration: underline;
    }
    &--active{
      background-color: $whiteBg;
      border-color: $whiteBg;
      span{
        text-decoration: none;
      }
    }
  }
}

.lab-card{
  padding: 29px 37px;
  background: #FFFFFF;
  border-radius: 40px;
  &:not(:last-child){
    margin-bottom: 15px;
  }
  &__wrap{
    display: grid;
    grid-template-columns: auto 195px 236px;
    gap: 20px 40px;
    margin-top: 25px;
  }
  &__link{
    height: 58px;
    width: 100%;
  }
  &__item{
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 18px;
    line-height: 189%;
  }
  &__icon{
    width: 16px;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    margin-right: 9px;
    img{
      max-width: 100%;
    }
  }

}

.lab-guide{
  padding: 75px 0 100px;
  &__text{
    margin-top: 30px;
  }
}

.consult-lab{
  background-color: #61A935;
  @media screen and (min-width: 992px){
    .consult{
      &__inner{
        grid-template-columns: 50% 40.452%;
      }
      &__content{
        order: -1;
      }
      &__desc{
        margin-top: 10px;
      }
      &__bottom{
        display: grid;
        grid-template-columns: 1fr 1fr;
        max-width: 766px;
        gap: 35px;
      }
      &__check{
        max-width: 340px;
        margin-top: 25px;
      }
      &__btn{
        margin-top: 25px;
        max-width: 310px;
      }
    }
  }
  .consult{
    &__btn{
      color: $secondary;
      &:hover{
        background-color: $secondary;
        color: #fff;
        path{
          fill:none;
        }
      }
    }
  }
  .fg{
    select{
      font-weight: 700;
      font-size: 20px;
    }
  }
}

.selected-analysis{
  margin-top: 20px;
  display: grid;
  grid-template-columns: 100%;
  gap: 15px;
  &__item{
    display: flex;
    justify-content: space-between;
    width: 330px;
    max-width: 100%;
    font-size: 18px;
    line-height: 23px;
    letter-spacing: -0.02em;
  }
  &__btn{
    margin-left: 5px;
    width: 24px;
    height: 24px;
    &:hover{
      opacity: .8;
    }
  }
}


@media screen and (max-width: 992px){
  .lab{
    padding: 39px 0 34px;
  }

  .lab-card{
    &__wrap{
      grid-template-columns: 100%;
    }
  }

  .lab-guide{
    padding: 38px 0 50px;
  }
}


@media screen and (max-width: 576px){
  .lab-card{
    padding: 15px 18px;
    border-radius: 20px;
  }
}
