  flex-direction: column;
  align-items: center;
  &__badge{
    position: absolute;
    top: 17px;
    left: 20px;
    padding: 0 18px;
    border-radius: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    background: #FF5353;
    letter-spacing: 0.06em;
    font-weight: 700;
    font-size: 18px;
    color: #fff;
  }
  &__img{
    position: relative;
    width: 100%;
    padding-bottom: 65.7%;
    border-radius: 40px;
    overflow: hidden;
    display: block;
    margin-bottom: 20px;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: scale(1.1);
      }
    }
  }
  &__title{
    width: 100%;
  }
  &__price{
    margin-top: 4px;
    width: 100%;
  }
  &__desc{
    width: 100%;
    margin: 4px 0 20px;
    font-size: 15px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient: vertical;
  }
  &__link{
    padding: 0 32px;
    margin: auto auto 0 0;
    height: 58px;
  }
}

.social-card{
  position: relative;
  border-radius: 50px;
  overflow: hidden;
  &__img{
    padding-bottom: 100%;
    position: relative;
    img{
      pointer-events: none;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }
  &__video{
    padding-bottom: 100%;
    &.video--played{
      &~.social-card__likes{
        display: none;
      }
    }
  }
  &__play{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($black,.7);
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    &-btn{
      width: 100px;
      height: 100px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(255, 255, 255, 0.4);
      backdrop-filter: blur(12px);
    }
  }
  &__likes{
    position: absolute;
    bottom: 22px;
    left: 22px;
    padding: 0 10px;
    display: flex;
    align-items: center;
    white-space: nowrap;
    background: #FFFFFF;
    border-radius: 23px;
    height: 32px;
    font-weight: 500;
    font-size: 15px;
    color: $primary;
    &:hover{
      cursor: pointer;
      opacity: .8;
    }
    &--active{
      font-weight: 800;
      .social-card__like{
        background-image: url(../img/heart2.svg);
      }
    }
  }
  &__like{
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    margin-right: 6px;
    background-image: url(../img/heart.svg);
    background-repeat: no-repeat;
    background-position: center;
  }
}

.clinic-card{
  overflow: hidden;
  &__title{
    margin-top: 32px;
    font-weight: 700;
    font-size: 26px;
    line-height: 131%;
    letter-spacing: -0.02em;
    color: $secondary;
  }
  &__link{
    width: 148px;
    height: 58px;
    margin-top: 32px;
  }
  .map{
    height: 388px;
    border-radius: 20px;
    overflow: hidden;
    .map-widget-layout-view__ruler{
      opacity: 0;
    }
  }

  .address__items{
    grid-template-columns: 1.2fr 1fr;
    gap: 10px 20px;
    margin: 18px 0 0;
  }
  .address__item{
    font-size: 16px;
    line-height: 169%;
  }
}

.licensy-card{
  border-radius: 20px;
  background-color: $whiteBg;
  padding: 21px;
  overflow: hidden;
  display: block;
  &__inner{
    position: relative;
    padding-bottom: 142%;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.detail-card{
  &__title{
    margin-bottom: 30px;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 530px) 497px;
    gap: 33px;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 20px;
    }
  }
}

.detail-card2{
  .detail-card__grid{
    grid-template-columns: 100%;
  }
}

.work-card{
  &__wrap{
    display: grid;
    grid-template-columns: calc(100% - 273px) 273px;
    border-radius: 40px;
    overflow: hidden;
  }
  &__img{
    padding-bottom: 80%;
    position: relative;
    .ba-slider{
      height: 100%;
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      img{
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }
  }
  &__content{
    height: 100%;
    border-radius: inherit;
    background-color: $whiteBg;
    padding: 35px 20px 45px 30px;
    width: calc(100% + 40px);
    margin-left: -40px;
    position: relative;
    z-index: 2;
  }
  &__info{
    display: flex;
    align-items: center;
  }
  &__name{
    font-size: 20px;
    line-height: 130%;
    letter-spacing: -0.04em;
    margin-top: 5px;
    display: block;
  }
  &__avatar{
    flex-shrink: 0;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    display: block;
    margin-right: 25px;
    &-inner{
      border-radius: 50%;
      overflow: hidden;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  &__items{
    margin-top: 59px;
  }
  &__item{
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: -0.04em;
    color: $primaryHover;
    &:not(:last-child){
      margin-bottom: 15px;
    }
    &-icon{
      width: 24px;
      flex-shrink: 0;
      margin-right: 10px;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__time{
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 169%;
    margin-top: 44px;
    &-icon{
      width: 17px;
      flex-shrink: 0;
      margin-right: 17px;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__desc{
    margin-top: 24px;
    font-size: 15px;
    line-height: 160%;
    letter-spacing: -0.04em;
  }
  .note-card__info-item{
    color: inherit;
    margin-top: 16px;
  }
}

.sale-card{
  border-radius: 40px;
  overflow: hidden;
  background-color: $whiteBg;
  display: flex;
  flex-direction: column;
  &.full{
    @media screen and (min-width: 992px){
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-column: 1/-1;
      gap: 30px;
      .sale-card{
        &__img{
          padding: 0;
          img{
            position: static;
          }
        }
      }
    }
  }
  &.single{
    gap: 10px;
    .sale-card{
      @media screen and (min-width: 992px){
        &__content{
          order: -1;
        }
      }
      &__desc{
        overflow: unset;
        display: block;
        -webkit-line-clamp: unset;
        -webkit-box-orient: unset;
        margin-bottom: 0;
        p{
          &:not(:last-child){
            margin-bottom: 10px;
          }
        }
      }
    }
  }
  &__img{
    width: 100%;
    padding-bottom: 66%;
    background-size: cover;
    border-radius: inherit;
    position: relative;
    display: block;
    img{
      border-radius: inherit;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__badges{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 22px 19px;
  }
  &__badge{
    border-radius: 23px;
    height: 32px;
    display: flex;
    align-items: center;
    background: #FFFFFF;
    color: $primary;
    padding: 0 14px;
    white-space: nowrap;
    font-weight: 700;
    font-size: 15px;
    &--green{
      color: #fff;
      background: $primary;
    }
  }
  &__content{
    padding: 48px 50px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    font-weight: 700;
    font-size: 26px;
    line-height: 115%;
    letter-spacing: -0.02em;
    width: 100%;
    display: block;
  }
  &__desc{
    width: 100%;
    margin-top: 25px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient: vertical;
  }
  &__info{
    width: 100%;
    margin: 25px 0;
    &-title{
      display: flex;
      align-items: center;
      font-size: 18px;
      line-height: 144%;
      letter-spacing: -0.04em;
      img{
        flex-shrink: 0;
        margin-right: 10px;
      }
    }
    .links{
      margin-top: -5px;
    }
    .link{
      margin-right: 30px;
      font-size: 16px;
    }
  }
  &__link{
    margin: auto auto 0 0;
    padding: 0 32px;
    height: 58px;
  }
}


@media screen and (max-width: 1300px){
  .detail-card{
    &__grid{
      grid-template-columns: calc(100% - 370px) 350px;
      gap: 20px;
    }
  }
}


@media screen and (max-width: 992px){
  .service-card{
    min-height: 368px;
  }

  .doctor-card{
    display: flex;
    flex-direction: column;
    &__img{
      position: relative;
      overflow: hidden;
      padding-bottom: 96%;
      img{
        height: 133%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    &__content{
      padding: 25px 15px 45px;
      flex-grow: 1;
    }
    &__desc{
      margin-top: 16px;
    }
    &__buttons{
      padding-top: 30px;
    }
  }

  .detail-card{
    &__title{
      margin-bottom: 20px;
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      order: -1;
      img{
        border-radius: 10px;
      }
    }
  }

  .work-card{
    &__wrap{
      grid-template-columns: 100%;
      border-radius: 10px;
    }
    &__content{
      border-radius: 10px;
      width: 100%;
      margin: -10px 0 0 0;
      padding: 20px;
    }
    &__desc{
      margin-top: 20px;
    }
    &__info{
      flex-direction: column-reverse;
      align-items: unset;
    }
    &__avatar{
      margin: 0 0 10px;
    }
    &__name{
      br{
        display: none;
      }
    }
    &__items,
    &__time{
      margin-top: 25px;
    }
    &__item{
      font-size: 16px;
    }
  }

  .sale-card{
    &__content{
      padding: 20px;
    }
    &__link{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .doctor-card{
    &__name{
      font-size: 20px;
    }
    &__buttons{
      flex-wrap: wrap;
    }
    &__btn{
      width: 100%;
      &:not(:last-child){
        margin: 0 0 10px;
      }
    }
  }

  .feedback-card{
    padding: 25px 15px 35px;
    &__top,
    &__top2{
      display: block;
    }
    &__for{
      margin-top: 10px;
    }
    &__date{
      margin-top: 10px;
    }
    &__text,
    &__more,
    &__course{
      font-size: 15px;
    }
    &__answer{
      align-items: unset;
      display: block;
      &-icon{
        margin: 0 0 10px;
        display: block;
      }
    }
  }

  .note-card{
    &__doctor{
      flex-direction: column-reverse;
      align-items: unset;
    }
    &__avatar{
      margin: 0 0 10px;
    }
    &__desc{
      margin-top: 10px;
      padding: 0;
    }
    &__info{
      padding: 15px;
    }
    &__img{
      border-radius: 10px;
    }
    &__badge{
      left: 15px;
    }
    &__title{
      font-size: 18px;
      min-height: 45px;
    }
  }

  .clinic-card{
    grid-template-columns: 100%;
    border-radius: 10px;
    &__content{
      border-radius: 0;
      padding: 20px;
    }
    &__map{
      order: -1;
      position: relative;
      padding-bottom: 100%;
      .map{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        margin: 0;
      }
    }
  }

  .social-card{
    border-radius: 20px;
    &__play{
      &-btn{
        width: 45px;
        height: 45px;
        svg{
          width: 12px;
          height: auto;
        }
      }
    }
    &__likes{
      bottom: 5px;
      left: 5px;
      padding: 0 7px;
      font-size: 12px;
      height: 22px;
    }
    &__like{
      width: 13px;
      height: 13px;
      background-size: 100% auto;
      margin-right: 3px;
    }
  }

  .service-card2{
    &__media{
      border-radius: 20px;
    }
  }

  .service-card3{
    &__img{
      border-radius: 20px;
    }
  }

  .sale-card{
    border-radius: 20px;
  }
}
