.doctor-edu{
  margin-top: 60px;
  &__title{
    text-align: center;
    border-bottom: 1px solid rgba(145, 149, 155, 0.3);
    padding-bottom: 20px;
    margin-bottom: 20px;
  }
}

.edu{
  &__items{
    margin: 0 auto;
    width: 915px;
    max-width: 100%;
    @include grid100gap(26px);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 5px;
      height: 100%;
      background-color: $primary;
    }
  }
}

.text24{
  font-size: 24px;
  line-height: 130%;
}

.text18{
  font-size: 18px;
  line-height: 155%;
}

.edu-item{
  position: relative;
  @include grid100gap(5px);
  &:before{
    content: '';
    position: absolute;
    top: 0;
    width: 31px;
    height: 31px;
    background-color: $primary;
    border-radius: 50%;
  }
  &:last-child{
    &:after{
      content: '';
      position: absolute;
      height: calc(100% - 31px);
      width: 31px;
      background-color: #fff;
      bottom: 0;
    }
  }
  @media screen and (min-width: 993px){
    width: calc(50% - 59px);
    &:nth-child(odd){
      margin-left: auto;
      &:after,
      &:before{
        right: 100%;
        margin-right: 44px;
      }
    }
    &:nth-child(even){
      text-align: right;
      &:after,
      &:before{
        left: 100%;
        margin-left: 44px;
      }
    }

  }
}

.doctor-edu{
  .edu__items._desktop{
    display: none;
  }
  @media screen and (min-width: 993px){
    .edu__items._mob{
      display: none;
    }
    .edu__items._desktop{

      display: grid;
      grid-template-columns: repeat(2,calc(50% - 3.72em));
      align-items: flex-start;
      justify-content: space-between;
      .edu-item{
        width: 100%;
      }
      .edu__items-col{
        @include grid100gap(32px);
        &:nth-child(1){
          padding-top: 48px;
          .edu-item{
            text-align: right;
            &:before{
              left: 100%;
              right: unset;
              margin: 0 0 0 44px;
            }
          }
        }
        &:nth-child(2){
          .edu-item{
            text-align: left;
            &:before{
              left: unset;
              right: 100%;
              margin: 0 44px 0 0;
            }
            &:last-child{
              &:after{
                background-color: transparent;
                //width: 5px;
                //background-color: $primary;
              }
            }
          }
        }
      }
    }
  }
}

.consult{
  .work-card{
    margin-top: 40px;
    &__content{
      color: $black;
    }
  }
  &__nav{
    display: flex;
    margin-top: 40px;
  }
  &__btn2{
    border: 1px solid #fff;
    border-radius: 50px;
    height: 59px;
    padding: 0 33px;
    &:hover{
      background-color: #fff;
      color: $black;
    }
  }
}


@media screen and (max-width: 1200px) {
  .consult-doctor2{
    .consult__inner{
      grid-template-columns: 30% 65%;
    }
  }
}


@media screen and (max-width: 992px) {
  .doctor-edu{
    &__title{
      text-align: left;
    }
  }

  .edu {
    &__items {
      gap: 24px;

      &:before {
        left: 14px;
        transform: unset;
      }
    }
  }

  .edu-item {
    padding-left: 40px;
    gap: 4px;

    &:after,
    &:before {
      right: unset;
      left: 0;
    }
  }

  .consult-doctor2{
    .consult__inner{
      grid-template-columns: 100%;
    }
  }
}
