.mistake {
  padding-bottom: 100px;
  &__inner {
    display: flex;
  }
  &__left {
    max-width: 542px;
    margin-right: 130px;
  }
  &__subtitle {
    font-weight: 700;
    font-size: 28px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: #AF3BB3;
  }
  &__text {
    font-weight: 700;
    font-size: 28px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: #2D343E;
  }
  &__links {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px 60px;
    margin-top: 50px;
    max-width: 440px;
  }
  &__link {
    font-size: 18px;
    line-height: 120%;
    letter-spacing: -0.02em;
    text-decoration-line: underline;
    color: #2D343E;
  }
  &__btn {
    width: 189px;
    height: 58px;
    margin-top: 50px;
  }
  &__img {
    margin-top: 100px;
    img {
      width: 100%;
      height: auto;
    }
  }
}

@media screen and (max-width: 1500px) {
  .mistake {
    &__left {
      margin-right: 50px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .mistake {
    &__inner {
      flex-direction: column;
    }
    &__left {
      margin: 40px 0 0;
    }
    &__right {
      order: -1;
    }
    &__img {
      margin-top: 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .mistake {
    &__subtitle {
      font-size: 24px;
    }
    &__text {
      font-size: 24px;
    }
    &__links {
      margin-top: 40px;
    }
  }
}

@media screen and (max-width: 576px) {
  .mistake {
    &__links {
      grid-template-columns: 1fr;
      gap: 20px;
    }
  }
}
