.main{
  padding: 8px 0 40px;
  color: #fff;
  .swiper{
    &-container{
      overflow: hidden;
      position: relative;
      background-image: url(../img/main-bg.webp),radial-gradient(105.99% 479.68% at 19.9% 81.37%, #5CA649 0%, #D5FFCA 100%, #9CE788 100%);
      background-size: cover;
      background-position: center;
      border-radius: 160px;
    }
    &-slide{
      display: flex;
      padding: 6.9% 8.5%;
      position: relative;
    }
    &-button{
      &-prev{
        left: 15px;
      }
      &-next{
        right: 15px;
      }
    }
    &-pagination{
      position: static;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 20px;
      &-bullet{
        width: 6px;
        height: 6px;
        background: #C2C2C2;
        opacity: 0.5;
        margin: 0 7px !important;
        &-active{
          width: 10px;
          height: 10px;
        }
      }
    }
  }
  &__content{
    max-width: 49%;
    flex-shrink: 0;
    margin-right: 50px;
    position: relative;
    z-index: 2;
  }
  &__date{
    font-weight: 700;
    font-size: 16px;
    line-height: 188%;
    letter-spacing: -0.02em;
    display: inline-flex;
    align-items: center;
    padding: 0 22px 0 13px;
    height: 40px;
    color: $primaryHover;
    position: relative;
    border-radius: 2px;
    overflow: hidden;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      border-width: 20px 13.333px 20px 33.333px;
      border-style: solid;
      border-color: #fff transparent #fff #fff;
    }
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      background-color: #fff;
      height: 100%;
      width: calc(100% - 45px);
    }
    span{
      position: relative;
      z-index: 2;
    }
  }
  &__title{
    margin-top: 85px;
  }
  &__desc{
    font-size: 18px;
    line-height: 144%;
    margin-top: 15px;
    letter-spacing: -0.02em;
    max-width: 525px;
  }
  &__btn{
    margin-top: 87px;
    width: 220px;
    height: 58px;
  }
  &__img{
    width: 100%;
    &-wrap{
      position: absolute;
      right: 8.5%;
      bottom: 0;
      width: 49%;
    }
  }
  &__brand{
    position: absolute;
    top: 2.207%;
    right: -12%;
    width: 46.4%;
  }
}

.advantages{
  overflow: hidden;
  padding: 60px 0;
  &__slider{
    margin-top: 85px;
  }
  .swiper{
    &-container{
      padding-bottom: 85px;
    }
    &-wrapper{
      align-self: stretch;
    }
    &-slide{
      width: 364px;
      max-width: calc(100% - 10px);
      height: auto;
    }
  }
}

.custom-block{
  padding: 60px 0;
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 68px;
  }
  &__link{
    padding: 0 32px;
    height: 58px;
    margin-left: 20px;
    flex-shrink: 0;
    svg{
      margin-right: 12px;
    }
  }
}

.slider{
  position: relative;
  .swiper{
    &-container{
      position: relative;
    }
    &-button{
      position: static;
      border: 1px solid #1F2123;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      margin-top: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0.2;
      &-next{
        margin-left: 10px;
      }
      &:hover{
        opacity: .8;
      }
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 100px;
  }
  &__nav{
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 100%;
    right: 0;
    margin-bottom: 87px;
    .swiper-button{
      position: static !important;
      &-next{
        margin-left: 10px !important;
      }
    }
  }
}

.services{
  @media screen and (min-width: 992px){
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(4, calc(25% - 22.5px));
        gap: 30px;
      }
      &-slide{
        margin: 0 !important;
        &:nth-child(3),
        &:nth-child(5),
        &:nth-child(8){
          grid-column: 3/5;
        }
        &:nth-child(4){
          grid-column: 1/3;
        }
      }
      &-scrollbar{
        display: none;
      }
    }
  }
  &__link{
    &:hover{
      path{
        stroke: #fff;
        fill:none;
      }
    }
  }
}

.doctors,
.feedbacks,
.works{
  .slider {
    .swiper{
      &-slide{
        width: 760px;
        max-width: calc(100% - 10px);
        height: auto;
      }
      @media screen and (min-width: 1300px){
        &-slide{
          width: calc(50% - 15px);
          max-width: calc(100% - 10px);
          height: auto;
        }
        &-container{
          overflow: hidden;
        }
      }
    }
  }
}

.doctors{
  &__slider{
    margin-top: 70px;
  }
  .slider__nav{
    margin-bottom: 70px;
  }
  &__all{
    width: 139px;
    height: 58px;
    margin-top: 50px;
  }
}

.feedbacks{
  background-color: $whiteBg;
  background-image: url(../img/brand2.svg);
  background-repeat: no-repeat;
  background-position: right 25% top 48px;
  padding: 110px 0 120px;
  margin-top: 80px;
  &--nobg{
    background-image: none;
  }
  .custom-block__top{
    margin: 0;
  }
  .slider {
    margin-top: 70px;
    .swiper{
      &-wrapper{
        align-items: unset;
      }
      &-slide{
        height: 100%;
      }
      &-button{
        position: absolute;
        margin: 0;
        &-prev{
          left: -75px;
        }
        &-next{
          right: -75px;
        }
      }
    }
  }
  &__link{
    margin-top: 69px;
    width: 283px;
    height: 58px;
  }
  .slider__nav{
    margin-bottom: 70px;
  }
}

.sites{
  margin-top: 45px;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 177px;
      height: auto;
    }
  }
}

.consult{
  background-image: url(../img/consult-bg.webp);
  background-color: #94339C;
  color: #fff;
  padding: 94px 0;
  &__inner{
    display: grid;
    grid-template-columns: 40.452% 50%;
    justify-content: space-between;
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 370px;
    }
  }
  &__title{
    font-size: 60px;
    line-height: 83%;
    letter-spacing: 0.01em;
  }
  &__desc{
    font-size: 20px;
    margin-top: 70px;
    line-height: 140%;
    letter-spacing: -0.02em;
    max-width: 680px;
    a{
      font-weight: 800;
      white-space: nowrap;
    }
  }
  &__form{
    margin-top: 47px;
  }
  &__check{
    max-width: 450px;
    margin-top: 70px;
  }
  &__btn{
    margin-top: 70px;
    width: 348px;
    height: 78px;
    font-size: 18px;
    max-width: 100%;
    svg{
      margin-right: 20px;
    }
    &:hover{
      path{
        fill:none;
        stroke:#fff;
      }
    }
  }
}

.address{
  padding: 60px 0;
  &--bottom{
    padding-bottom: 0;
  }
  &__title2{
    text-align: center;
  }
  &__tags{
    margin-top: 42px;
    justify-content: center;
  }
  &__content{
    margin-top: 57px;
    height: 730px;
    position: relative;
  }
  &__card{
    position: absolute;
    padding: 63px 43px 73px;
    background: #FFFFFF;
    border-radius: 50px;
    top: 50%;
    left: calc((100% - 1550px) / 2);
    transform: translateY(-50%);
    z-index: 2;
    width: 530px;
    @media screen and (max-width: 1610px){
      left: 30px;
    }
  }
  &__title{
    font-weight: 700;
    font-size: 30px;
    line-height: 113%;
    letter-spacing: -0.02em;
  }
  &__items{
    margin-top: 43px;
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
  }
  &__item{
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 189%;
    &-icon{
      flex-shrink: 0;
      width: 20px;
      margin-right: 14px;
      img{
        width: 100%;
      }
    }
    &>*{
      line-height: inherit;
    }
  }
  &__buttons{
    display: flex;
    align-items: center;
    margin-top: 50px;
  }
  &__btn{
    padding: 0 32px;
    height: 58px;
    //font-size: 15px;
    &:not(:last-child){
      margin-right: 16px;
    }
  }
}

.map{
  height: 100%;
}

.notes{
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 499px;
      height: auto;
      max-width: calc(100% - 10px);
    }
    @media screen and (min-width: 1300px){
      &-slide{
        width: calc(33.33% - 20px);
      }
      &-container{
        overflow: hidden;
      }
    }
  }
  &__title{
    span{
      color: $primaryHover;
    }
  }
}

.social{
  padding: 140px 0 180px;
  overflow: hidden;
  &--about{
    padding: 120px 0 60px;
  }
  &__title{
    text-align: center;
  }
  &__desc{
    text-align: center;
    margin: 30px auto 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 167%;
    letter-spacing: -0.02em;
    max-width: 655px;
    color: #969EAA;
  }
  &__content{
    position: relative;
    height: 760px;
    overflow: hidden;
    margin-top: 64px;
  }
  .swiper{
    &-wrapper{
      margin: 0 auto;
      justify-content: center;
    }
  }
  .social-card{
    width: 350px;
  }
  &__block{
    padding: 44px 0 55px;
    border-radius: 50px;
    background-color: $secondary;
    color: #fff;
    width: 350px;
    height: 350px;
    &-img{
      display: block;
      margin: 0 auto 35px;
      width: 159px;
      max-width: 46%;
    }
  }
  &__subtitle{
    font-weight: 700;
    font-size: 22px;
    line-height: 136%;
    text-align: center;
    flex-shrink: 0;
  }
  &__items{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
  }
  &__item{
    background: #FFFFFF;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 5px;
    &:hover{
      opacity: .8;
    }
  }
}


@media screen and (max-width: 1500px){
  .main{
    .swiper{
      &-button{
        &-prev{
          left: 0;
        }
        &-next{
          right: 0;
        }
      }
    }
  }

  .services{
    @media screen and (min-width: 992px){
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(3, calc(33.33% - 20px));
        }
        &-slide{
          margin: 0 !important;
          &:nth-child(3){
            grid-column: 2/4;
            grid-row: 1/2;
          }
          &:nth-child(4){
            grid-column: 1/3;
            grid-row: 2/3;
          }

          &:nth-child(5){
            grid-column: 2/4;
            grid-row: 3/4;
          }
          &:nth-child(8){
            grid-column: 1/3;
            grid-row: 4/5;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 1300px){
  .consult{
    &__title{
      font-size: 50px;
    }
    &__desc{
      font-size: 18px;
    }
    &__form,
    &__check,
    &__btn{
      margin-top: 30px;
    }
    &__img{
      max-width: 500px;
    }
  }
}


@media screen and (max-width: 1200px){
  .main{
    .swiper{
      &-container{
        border-radius: 80px;
      }
      &-slide{
        padding: 4% 5%;
      }
    }
    &__title,
    &__btn{
      margin-top: 45px;
    }
  }
}


@media screen and (max-width: 1100px){
  .social {
    padding: 60px 0;
    &--about{
      padding: 30px 0;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding-bottom: 20px;
    .swiper{
      &-container{
        border-radius: 10px;
      }
      &-slide{
        display: block;
        padding: 20px 20px 0;
      }
      &-button{
        &-prev,
        &-next{
          display: none;
        }
      }
    }
    &__content{
      max-width: 100%;
      margin: 0 0 20px;
    }
    &__title{
      max-width: 100%;
      margin-top: 20px;
    }
    &__desc{
      margin-top: 10px;
    }
    &__btn{
      margin-top: 20px;
    }
    &__img{
      max-width: 500px;
      &-wrap{
        position: static;
      }
    }
    &__brand{
      display: none;
    }
  }

  .advantages{
    padding: 30px 0;
    &__slider{
      margin-top: 40px;
    }
    .swiper{
      &-container{
        padding-bottom: 40px;
      }
    }
  }

  .custom-block{
    padding: 30px 0;
    &__inner{
      padding-bottom: 80px;
      position: relative;
    }
    &__top{
      margin-bottom: 40px;
    }
    &__link{
      margin: 0;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
    }
  }

  .slider{
    &__nav{
      display: none;
    }
    &__top{
      padding: 0;
    }
  }

  .services,
  .doctors,
  .works{
    .slider .swiper{
      &-wrapper{
        align-self: stretch;
      }
      &-slide{
        width: 364px;
        max-width: calc(100% - 10px);
        height: auto;
      }
    }
  }

  .doctors{
    .custom-block{
      &__inner{
        padding-bottom: 0;
      }
    }
  }

  .feedbacks{
    margin-top: 20px;
    .custom-block__title{
      font-size: 30px;
      br{
        display: none;
      }
    }
    .slider{
      margin-top: 35px;
    }
  }

  .sites{
    margin-top: 35px;
  }

  .consult{
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      display: none;
    }
    &__title{
      font-size: 35px;
    }
    &__btn{
      font-size: 14px;
      svg{
        margin-right: 10px;
      }
    }
    &__desc{
      font-size: 17px;
      margin-top: 40px;
    }
  }

  .address{
    padding: 30px 0;
    &--bottom{
      padding-bottom: 0;
    }
    &__tags{
      margin-top: 5px;
    }
    &__content{
      height: unset;
      margin-top: 30px;
    }
    &__card{
      position: static;
      background-color: $whiteBg;
      transform: unset;
      width: 100%;
      padding: 20px;
      border-radius: 0;
    }
    &__items{
      margin-top: 20px;
      gap: 10px;
    }
  }

  .map{
    height: 400px;
  }

  .social{
    padding: 30px 0;
    &__content{
      margin-top: 30px;
    }
  }
}


@media screen and (max-width: 576px){
  .address{
    &__buttons{
      flex-wrap: wrap;
      margin-top: 30px;
    }
    &__btn{
      width: 100%;
      padding: 0;
      &:not(:last-child){
        margin: 0 0 10px;
      }
    }
  }

  .social {
    &__title,
    &__desc{
      text-align: left;
    }
    &__title{
      font-size: 30px;
    }
    &__desc{
      font-size: 15px;
      margin-top: 15px;
    }
    &__content{
      height: 280px;
      margin-top: 15px;
    }
    &__subtitle{
      font-size: 17px;
    }
    .social-card,
    &__block{
      width: 130px;
      border-radius: 20px;
      &-img{
        display: none;
      }
    }
    &__block{
      height: 130px;
      padding: 45px 0;
    }
    &__item{
      margin: 0 2px;
      width: 19px;
      height: 19px;
      img{
        max-width: 70%;
        max-height: 70%;
      }
      &:last-child{
        img{
          max-width: 100%;
          max-height: 100%;
        }
      }
    }
  }
}


@media screen and (max-width: 450px){
  .slider.animated{
    .swiper-container{
      animation: shake 2.5s cubic-bezier(.36,.07,.19,.97);
      animation-iteration-count: 1;
    }
  }
}


@keyframes shake {
  0% {
    transform: translate3d(0px, 0, 0);
  }
  50% {
    transform: translate3d(-30px, 0, 0);
  }
  100% {
    transform: translate3d(0px, 0, 0);
  }
}
