@import "../vars.scss";
.breadcrumbs {
    margin-bottom: 57px;
    padding: 16px 0;
    background: #F8F8F8;
    &__item {
        font-size: 14px;
        line-height: 18px;
        letter-spacing: -0.02em;
        display: inline;
        color: #91959B;
        &:last-child {
            pointer-events: none;
        }
        &:hover{
            color: $black;
        }
        &:not(:first-child) {
            &:before {
                content: '';
                display: inline-block;
                width: 15px;
                height: 15px;
                background-image: url(../img/right.svg);
                background-repeat: no-repeat;
                background-position: center;
                margin: 0 2px 0 3px;
                transform: translateY(3px);
            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        margin-bottom: 30px;
    }
}
