.display1{
  font-weight: 700;
  font-size: 70px;
  line-height: 93%;
}

.display2{
  font-weight: 700;
  font-size: 48px;
  line-height: 104%;
}

.display3{
  font-weight: 700;
  font-size: 22px;
  line-height: 136%;
  letter-spacing: -0.02em;
}

.display4 {
  font-size: 55px;
  line-height: 120%;
  letter-spacing: -0.04em;
}

.display5{
  font-weight: 700;
  font-size: 22px;
  line-height: 136%;
  letter-spacing: -0.04em;
}

.display6{
  font-weight: 700;
  font-size: 35px;
  line-height: 107%;
  letter-spacing: -0.04em;
}

.primary-text{
  color: $primaryHover;
}

.secondary-text{
  color: $secondary;
}

.gray-text{
  color: $gray;
}

.extra-bold{
  font-weight: 800;
}

.bold-text {
  font-weight: 700;
}

.medium-text{
  font-weight: 500;
}

.body-text{
  font-size: 16px;
  line-height: 156%;
  letter-spacing: -0.02em;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
}

.tdu{
  text-decoration: underline;
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 1500px){
  .display1{
    font-size: 60px;
  }
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 40px;
  }

  .display2{
    font-size: 35px;
  }

  .display4 {
    font-size: 30px;
  }

  .display5{
    font-size: 20px;
  }

  .display6{
    font-size: 24px;
  }

  .body-text{
    font-size: 14px;
  }


}
