.footer{
  padding: 60px 0 28px;
  background-color: $whiteBg;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__left{
    display: flex;
    flex-direction: column;
  }
  &__logo{
    width: 338px;
    margin-bottom: 25px;
    transform: translateX(-21px);
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 35px;
    }
  }
  &__title{
    font-weight: 800;
    font-size: 16px;
    line-height: 172%;
    color: $primaryHover;
    margin-bottom: 12px;
    letter-spacing: -0.02em;
  }
  &__links{
    &-wrap{
      display: grid;
      grid-template-columns: auto auto;
      gap: 90px;
    }
  }
  &__link{
    letter-spacing: -0.02em;
    font-size: 16px;
    line-height: 238%;
    display: block;
  }
  &__right{
    .footer__title{
      margin-bottom: 20px;
      line-height: 144%;
    }
    .footer__group{
      &:not(:last-child){
        margin-bottom: 40px;
      }
    }
  }
  .social__items{
    margin: 0 0 0 -5px;
    justify-content: unset;
  }
  .social__item{
    width: 25px;
    height: 25px;
    background-color: transparent;
  }
  &__phone{
    display: block;
    font-weight: 700;
    font-size: 22px;
    line-height: 164%;
  }
  &__clinic,
  &__mail,
  &__time{
    font-weight: 700;
    font-size: 17px;
    line-height: 144%;
  }
  &__clinic{
    display: flex;
    align-items: center;
    svg{
      margin-right: 14px;
    }
    span{
      text-decoration: underline;
    }
  }
  &__logo2 {
    width: 143px;
    display: block;
    padding-top: 100px;
    margin-top: auto;
    svg {
      width: 100%;
      height: auto;
    }
    &:hover svg>g {
      opacity: .6
    }
  }
  &__bottom{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 50px;
  }
  &__protect,
  &__privacy{
    font-size: 14px;
    line-height: 190%;
    color: rgba(#2D343E,.4);
    letter-spacing: -0.02em;
  }
  &__consult{
    font-family: $font2;
    font-size: 26px;
    line-height: 146%;
    color: rgba(#2D343E,.2);
    letter-spacing: 7.5px;
  }
}

.scroll-up {
  position: fixed;
  width: 114px;
  height: 114px;
  border-radius: 50%;
  bottom: 50px;
  right: 35px;
  z-index: 8;
  opacity: 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 27px 0;
  color: #fff;
  font-weight: 500;
  font-size: 20px;
  line-height: 230%;
  &--active {
    opacity: 1;
  }
}


@media screen and (max-width: 1500px){
  .footer{
    &__consult{
      letter-spacing: 5px;
    }
  }
}


@media screen and (max-width: 1300px){
  .footer{
    &__logo{
      width: 210px;
    }
    &__link{
      font-size: 15px;
    }
    &__consult{
      letter-spacing: 2px;
      font-size: 23px;
    }
  }
}


@media screen and (max-width: 1100px){
  .footer{
    padding-top: 40px;
    &__links{
      display: none;
    }
    &__bottom{
      display: block;
    }
    &__consult{
      margin-top: 15px;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__top{
      display: block;
    }
    &__right{
      .footer__group{
        &:not(:last-child){
          margin-bottom: 25px;
        }
      }
      .footer__title{
        margin-bottom: 10px;
      }
    }
    &__logo2{
      margin: 0 0 30px;
    }
    &__bottom{
      margin-top: 45px;
    }
    &__consult{
      font-size: 20px;
    }
  }

  .scroll-up{
    padding: 0;
    justify-content: center;
    width: 50px;
    height: 50px;
    span{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    .social__item{
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
  }
}
