.feedback-page{
  padding-bottom: 50px;
  &__desc{
    margin-top: 30px;
  }
}


.feedbacks-block{
  overflow: hidden;
  padding: 70px 0 76px;
  background-color: $whiteBg;
  &__grid{
    margin-top: 73px;
  }
}

.feedbacks-filter{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 88px;
  &__selects{
    display: flex;
    align-items: center;
    flex-shrink: 0;
    select{
      padding: 0 46px 0 16px;
      background-color: #FFFFFF;
      border-radius: 50px;
      height: 44px;
      font-size: 16px;
      letter-spacing: -0.02em;
      border: none;
      margin: 12px 0 0 15px;
      background-image: url(../img/select-arrow.svg);
      background-position: right 16px center;
      background-repeat: no-repeat;
      -webkit-appearance: none;
      -moz-appearance: none;
      -ms-appearance: none;
      -o-appearance: none;
      appearance: none;
      &::-ms-expand {
        display: none; // IE
      }
    }
  }
  &__check{
    position: relative;
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      z-index: 2;
      opacity: 0;
      &:checked{
        &+label{
          font-weight: 700;
          color: $secondary;
          border-color: $secondary;
        }
      }
    }
    label{
      white-space: nowrap;
      height: 40px;
      border: 1px solid #D9D9D9;
      border-radius: 50px;
      display: flex;
      align-items: center;
      padding: 0 16px;
      font-size: 16px;
      letter-spacing: -0.02em;
      margin: 12px 8px 0 0;
    }
  }
}


@media screen and (max-width: 1300px){
  .feedbacks-block{
    &__grid{
      grid-template-columns: 100% !important;
    }
  }
}


@media screen and (max-width: 992px){
  .feedbacks-block{
    padding: 35px 0;
    &__grid{
      margin-top: 35px;
    }
  }

  .feedback-page{
    &__top{
      display: block;
    }
    &__btn{
      margin: 20px 0 0;
      width: 100%;
    }
  }

  .feedbacks-filter{
    display: block;
    &__selects{
      margin-top: 10px;
      select{
        &:first-child{
          margin-left: 0;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .feedbacks-filter{
    &__selects{
      display: block;
      select{
        width: 100%;
        margin-left: 0;
      }
    }
  }
}
