.sale-page{
  padding-bottom: 150px;
  &__title{
    margin-bottom: 45px;
  }
  &__grid{
    margin-top: 45px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
}

.sales{
  padding: 100px 0;
  .slider {
    margin-top: 70px;
    &__nav{
      margin-bottom: 70px;
    }
    .swiper{
      &-container{
        overflow: hidden;
      }
      &-slide{
        width: calc(50% - 15px);
        max-width: calc(100% - 10px);
        height: auto;
      }
    }
  }
  &__link{
    margin-top: 70px;
    width: 140px;
    height: 58px;
  }
}


@media screen and (max-width: 992px){
  .sale-page{
    padding-bottom: 50px;
  }

  .sales{
    padding: 50px 0;
    .slider {
      margin-top: 30px;
      .swiper{
        &-container{
          overflow: unset;
        }
        &-slide{
          width: 364px;
        }
      }
    }
    &__link{
      width: 100%;
      margin-top: 30px;
    }
  }
}


@media screen and (max-width: 800px){
  .sale-page{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


