@media screen and (min-width: 992px){
  .works{
    .custom-block__inner{
      position: relative;
    }
    .custom-block__top{
      align-items: unset;
      &-right{
        padding-left: 130px;
        flex-shrink: 0;
      }
      &-buttons{
        position: absolute;
        display: flex;
        top: 0;
        right: 280px;
        .swiper{
          &-button{
            &-prev,
            &-next{
              position: static;
              margin: 0 0 0 15px;
              width: 58px;
              height: 58px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 992px){
  .works{
    .custom-block__inner{
      padding-bottom: 140px;
    }
    .custom-block__top{
      &-buttons{
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        left: 0;
        width: 100%;
        bottom: 70px;
        .swiper{
          &-button{
            &-prev,
            &-next{
              position: static;
              margin: 0 0 0 15px;
              width: 58px;
              height: 58px;
            }
          }
        }
      }
    }
  }
}

.tax-form__inputs,
.tax-form__inputs2{
  grid-template-columns: 1fr 1fr;
  grid-column: 1/-1;
  gap: 25px;
  display: none;
  &._active{
    display: grid;
  }
  @media screen and (max-width: 992px){
    grid-template-columns: 100%;
  }
}

