.docs{
  padding-bottom: 110px;
  &__group{
    &:not(:last-child){
      margin-bottom: 94px;
    }
  }
  &__title{
    margin-bottom: 47px;
  }
  &__content{
    padding: 52px;
    border-radius: 20px;
    background-color: $whiteBg;
  }
  &__info{
    display: grid;
    grid-template-columns: 450px auto;
    gap: 20px;
    &:not(:last-child){
      margin-bottom: 50px;
      padding-bottom: 50px;
      border-bottom: 1px solid #EAE8E8;
    }
    a{
      color: $primaryHover;
    }
  }
  &__subtitle{
    letter-spacing: -0.03em;
    font-size: 26px;
    line-height: 115%;
    font-weight: 700;
    color: $primaryHover;
    max-width: 330px;
  }
  &__contacts{
    &-group{
      &:not(:last-child){
        margin-bottom: 35px;
      }
    }
    &-title{
      font-size: 18px;
      margin-bottom: 10px;
      line-height: 24px;
    }
    &-desc{
      a{
        text-decoration: underline;
      }
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 30px;
    grid-auto-flow: dense;
  }
  &__item{
    display: flex;
    align-items: center;
    border-radius: 20px;
    min-height: 79px;
    padding: 14px 11px 14px 20px;
    border: 1px solid $primaryHover;
    &:hover{
      background-color: $primaryHover;
      color: #fff;
      .docs__item-icon{
        background-image: url(../img/doc2.svg);
      }
    }
    &-icon{
      width: 16px;
      height: 16px;
      display: block;
      flex-shrink: 0;
      margin-right: 15px;
      background-image: url(../img/doc.svg);
      background-repeat: no-repeat;
      background-position: center;
    }
    &--big{
      grid-column: span 2;
    }
  }
}


@media screen and (max-width: 1300px){
  .docs{
    &__items{
      grid-template-columns: 1fr 1fr;
    }
    &__info{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .docs{
    padding-bottom: 55px;
    &__group{
      &:not(:last-child){
        margin-bottom: 30px;
      }
    }
    &__title{
      margin-bottom: 25px;
    }
    &__content{
      padding: 20px;
    }
    &__info{
      &:not(:last-child){
        margin-bottom: 30px;
        padding-bottom: 30px;
      }
    }
    &__subtitle{
      margin-bottom: 15px;
      font-size: 20px;
    }
    &__items{
      grid-template-columns: 100%;
      gap: 25px;
    }
    &__item{
      grid-column: 1/2;
    }
  }
}
