@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/inputs';
@import 'components/grid';
@import 'components/before-after';

.tags{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.tag{
    display: flex;
    align-items: center;
    padding: 0 16px;
    height: 40px;
    border-radius: 50px;
    border: $border;
    margin: 12px 8px 0 0;
    font-size: 16px;
    &--active{
        font-weight: 700;
        border-color: $secondary;
        pointer-events: none;
        color: $secondary;
    }
    &:hover{
        border-color: $secondary;
        color: $secondary;
        cursor: pointer;
    }
    &__content{
        display: none;
        &--active{
            display: block;
        }
    }
}

.links{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    &--small{
        @media screen and (min-width: 992px){
            .link{
                margin-right: 60px;
            }
        }
    }
}

.link{
    margin: 16px 100px 0 0;
    font-size: 18px;
    line-height: 133%;
    text-decoration-line: underline;
    &--active{
        font-weight: 700;
    }
}

.video{
    position: relative;
    padding-bottom: 50%;
    video{
        pointer-events: none;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
    }
    &--played{
        video{
            pointer-events: unset;
        }
        .video__play{
            display: none;
        }
    }
}

.nav-block{
    margin-top: 57px;
}

.more-btn{
    height: 57px;
    width: 100%;
    font-size: 16px;
    svg{
        margin-left: 12px;
    }
    &:hover{
        path{
            fill: none;
            stroke: #fff;
        }
    }
}

.pagination{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 27px;
    &__item,
    &__next{
        border-radius: 50px;
        border: $border;
        height: 34px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 10px;
        min-width: 34px;
        font-weight: 700;
        font-size: 14px;
        flex-shrink: 0;
        margin: 0 8px;
        &:hover,
        &--active{
            background-color: $primaryHover;
            border-color: $primaryHover;
            color: #fff;
            path{
                stroke: #fff;
            }
        }
        &--active{
            pointer-events: none;
        }
        &:first-child{
            margin-left: 0;
        }
    }
    &__next{
        padding: 0 16px;
        margin-right: 0;
        height: 36px;
        svg{
            margin-left: 10px;
        }
    }
    &__dots{
        font-weight: 700;
        font-size: 14px;
        margin: 0 10px;
    }
}

.hidden-p:not(.show-p){
    p:nth-child(4)~p{
        display: none;
    }
}

.show-p{
    ._show-hidden-text{
        font-size: 0;
        &:before{
            content: '';
            font-size: 15px;
        }
    }
}


@media screen and (max-width: 992px){
    .fg{
        margin-bottom: 20px;
    }
    .mcheck{
        label{
            padding-left: 45px;
            font-size: 14px;
        }
    }

    .link{
        margin-right: 20px;
        font-size: 16px;
    }
    .tags{
        justify-content: unset;
        overflow-x: auto;
        flex-wrap: nowrap;
        width: calc(100% + 40px);
        padding-left: 20px;
        margin-left: -20px;
        &::-webkit-scrollbar {
            width: 0;
            height: 0;
        }
    }
    .tag{
        flex-shrink: 0;
    }
}


@media screen and (max-width: 576px){
    .pagination{
        &__next{
            padding: 0;
            width: 39px;
            span{
                display: none;
            }
            svg{
                margin: 0;
            }
        }
    }
}


@media screen and (max-width: 450px){
    .tags.animated{
        .tag{
            animation: shake 2.5s cubic-bezier(.36,.07,.19,.97);
            animation-iteration-count: 1;
        }
    }
    .anim.animated{
        animation: shake 2.5s cubic-bezier(.36,.07,.19,.97);
        animation-iteration-count: 1;
    }
}
