.doctors-grid,
.doctors-grid2,
.clinics-grid,
.notes-grid{
  display: grid;
  gap: 30px;
}

.doctors-grid{
  grid-template-columns: repeat(2,calc(50% - 15px));
}

.clinics-grid{
  grid-template-columns: repeat(3,calc((100% / 3) - 20px));
}

.doctors-grid2{
  grid-template-columns: repeat(4,calc(25% - 22.5px));
}

.notes-grid{
  grid-template-columns: repeat(3,calc(33.33% - 20px));
}

@media screen and (max-width: 1500px){
  .doctors-grid{
    .doctor-card{
      @media screen and (min-width: 1300px){
        &__buttons{
          display: grid;
          grid-template-columns: 100%;
          gap: 10px;
          .doctor-card__btn{
            margin: 0 !important;
          }
        }
      }
    }
  }

  .doctors-grid2{
    grid-template-columns: repeat(3,calc(33.33% - 20px));
  }

  .clinics-grid{
    grid-template-columns: repeat(2,calc(50% - 15px));
  }
}


@media screen and (max-width: 1300px){
  .doctors-grid,
  .clinics-grid{
    grid-template-columns: 100%;
  }
}

@media screen and (max-width: 1200px){
  .doctors-grid2,
  .notes-grid{
    grid-template-columns: repeat(2,calc(50% - 15px));
  }
}


@media screen and (max-width: 992px){
  .doctors-grid,
  .doctors-grid2{
    grid-template-columns: repeat(2,calc(50% - 5px));
    gap: 10px;
    .doctor-card{
      @media screen and (min-width: 600px){
        &__buttons{
          display: grid;
          grid-template-columns: 100%;
          gap: 10px;
          .doctor-card__btn{
            margin: 0 !important;
          }
        }
      }
    }
  }
}


@media screen and (max-width: 768px){
  .notes-grid{
    grid-template-columns: 100%;
  }
}


@media screen and (max-width: 600px){
  .doctors-grid,
  .doctors-grid2{
    grid-template-columns: 100%;
    &.scroll{
      display: flex;
      gap: unset;
      overflow-x: auto;
      width: calc(100% + 40px);
      padding: 0 20px;
      margin-left: -20px;
      .doctor-card{
        width: 400px;
        max-width: calc(100% - 10px);
        height: auto;
        flex-shrink: 0;
        &:not(:last-child){
          margin-right: 15px;
        }
      }
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
  }
}
