.job-openings {
    padding: 0 0 55px;
    &__inner {
        display: flex;
        margin-top: 40px;
        background: #F8F8F8;
        border-radius: 40px;
    }
    &__left {
        width: calc(100% - 870px);
        padding: 51px 0 51px 54px;
    }
    &__right {
        width: 760px;
        margin-left: 110px;
    }
    &__img {
        img {
            border-radius: 40px;
            width: 100%;
            height: auto;
        }
    }
}

.advantages-two {
    overflow: hidden;
    &__cards {
        display: grid;
        grid-template-columns: repeat(5,1fr);
        gap: 24px;
        margin-top: 50px;
    }
}

.advantages-two__card{
    background: #F8F8F8;
    border-radius: 40px;
    padding: 63px 30px 55px;
    .advantage-card__icon{
        background: transparent !important;
        width: 100%;
        height: 85px;
        justify-content: unset;
        img{
            max-height: 100%;
        }
    }
    &-title{
        color: $secondary !important;
    }
    &-desc{
        margin-top: 10px;
        font-size: 16px;
        line-height: 24px;
    }
}

.our-clinics {
    overflow: hidden;
    padding: 60px 0 50px;
    &__slider {
        margin-top: 50px;
    }
    .slider__nav{
        margin-bottom: 50px;
    }
    .swiper-slide {
        width: 498px;
        max-width: calc(100% - 10px);
        height: auto;
    }
    .swiper{
        @media screen and (min-width: 1300px){
            &-slide{
                width: calc(33.33% - 20px);
            }
            &-container{
                overflow: hidden;
            }
        }
    }
    &__img {
        position: relative;
        padding-bottom: 65.5%;
        img {
            border-radius: 20px;
            height: 100%;
            width: 100%;
            -o-object-fit: cover;
            object-fit: cover;
            position: absolute;
            top: 0;
            left: 0;
        }
    }
}

.job-openings2 {
    padding: 50px 0 100px;
    &__tags {
        margin-top: 40px;
    }
    &__cards {
        margin-top: 40px;
    }
    .job-openings2__card {
        width: 100% !important;
        max-width: 100% !important;
    }
}

@media screen and (max-width: 1600px) {
    .job-openings {
        &__left {
            width: calc(100% - 750px);
        }
        &__right {
            width: 700px;
            margin-left: 50px;
        }
        &__img {
            height: 100%;
            img {
                height: 100%;
            }
        }
    }
}

@media screen and (max-width: 1500px) {
    .advantages-two {
        &__cards {
            grid-template-columns: 1fr 1fr 1fr;
        }
        &__card-desc {
            br {
                display: none;
            }
        }
    }
}

@media screen and (max-width: 1400px) {
    .job-openings {
        &__inner {
            flex-flow: column wrap;
        }
        &__left {
            width: 100%;
            padding: 0 42px 42px 42px;
            margin-top: 50px;
        }
        &__right {
            width: 100%;
            order: -1;
            margin-left: 0;
        }
        &__img {
            img {
                height: auto;
            }
        }
    }
}

@media screen and (max-width: 992px) {
    .job-openings {
        padding: 0 0 20px;
        &__inner {
            margin-top: 20px;
        }
        &__left {
            margin-top: 30px;
            padding: 0 30px 30px;
        }
    }

    .advantages-two {
        &__cards {
            grid-template-columns: 1fr 1fr;
            margin-top: 30px;
        }
        .advantages-two__card {
            padding: 30px 15px;
        }
    }

    .our-clinics {
        padding: 30px 0;
        &__slider {
            margin-top: 30px;
        }
    }

    .job-openings2 {
        padding: 30px 0 60px;
        &__tags {
            margin-top: 30px;
        }
        &__cards {
            margin-top: 30px;
        }
    }
}

@media screen and (max-width: 576px) {
    .job-openings {
        &__inner{
            border-radius: 20px;
        }
        &__img{
            img{
                border-radius: 20px;
            }
        }
        &__left {
            margin-top: 0;
            padding: 30px 15px;
        }
    }

    .advantages-two {
        &__cards {
            grid-template-columns: 1fr;
            gap: 15px;
        }
        &__card{
            text-align: center;
            .advantage-card{
                &__icon{
                    margin-left: auto;
                    margin-right: auto;
                    display: block;
                }
            }
        }
    }
}
